import React, { PropTypes, Component } from 'react';
import style from './salesforce.css';
import SalesforceInfo from './SalesforceInfo';
import AccountInfo from './AccountInfo';
import SalesforceBuggonGroup from './SalesforceButtonGroup';
import { contactInfo, LeadInfo } from '../../../constants/enums/salesforceInfoType';
import { shouldComponentUpdateGen } from '../../../utils/helper';
import CustomForm from './CustomForm';
import * as localStorage from '../../../utils/localStorage';

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'Salesforce',
  ['visitorId', 'editType', 'ifSalesforceLoading', 'salesforceSubmitError',
    'ifShowLoading', 'leadId', 'contactId',
    'statusInfo', 'salesforceAccountFields',
    'salesforceContactFields', 'salesforceLeadFields', 'waiting', 'errorMessage', 'getSFObjectFieldsError'], ['showForm']
);

class Salesforce extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showForm: props.showForm,
    };
    if (!props.showForm) {
      localStorage.deleteItem('salesforceFormState');
      localStorage.deleteItem('salesforceFormScrollTop');
    }
  }
  componentWillReceiveProps(nextProps) {
    if (this.props.visitorId !== nextProps.visitorId || this.props.chatId !== nextProps.chatId) {
      this.setState({
        showForm: false,
      });
    } else {
      this.setState({
        showForm: nextProps.showForm,
      });
    }
  }
  shouldComponentUpdate(nextProps, nextState) {
    return shouldComponentUpdateHelper(this.props, nextProps, nextState);
  }
  componentDidUpdate() {
    if (!this.state.showForm) {
      localStorage.deleteItem('salesforceFormState');
      localStorage.deleteItem('salesforceFormScrollTop');
    }
  }
  render() {
    if (!this.props.visitorId) return null;
    const { statusInfo, salesforceAccountFields, salesforceContactFields,
      salesforceLeadFields, onEditOrCreate, onCreateCase, visitorId,
      ifShowLoading, onAttachToContact, onAttachToLead, leadId,
      contactId, editType, ifSalesforceLoading, onCancelClick,
      onSubmitSalesforceForm, salesforceSubmitError, waiting, errorMessage, chatExists,
      getSFObjectFieldsError, ...rest } = this.props;
    let formTitle = '';
    if (editType === contactInfo) {
      if (contactId) {
        formTitle = 'Edit Contact';
      } else {
        formTitle = 'Create Contact & Attach Case';
      }
    } else if (editType === LeadInfo) {
      if (leadId) {
        formTitle = 'Edit Lead';
      } else {
        formTitle = 'Create Lead & Attach Task';
      }
    }
    return (
      <div>
        <div className={style.Loading} style={{ display: ifShowLoading ? 'block' : 'none' }}>
          Loading...
        </div>
        <div style={{ display: !ifShowLoading ? 'block' : 'none' }}>
          <div className={style.salesforce} style={{ display: this.state.showForm ? 'none' : 'block' }}>
            <div className={style.errorMessage} style={{ display: !waiting && (errorMessage || getSFObjectFieldsError) ? 'block' : 'none' }}>
              <span>{errorMessage || getSFObjectFieldsError}</span>
            </div>
            <SalesforceBuggonGroup
              statusInfo={statusInfo}
              onCreateContact={(type) => { onEditOrCreate(type); }}
              onCreateLead={(type) => { onEditOrCreate(type); }}
              onCreateCase={onCreateCase}
              onAttachToLead={onAttachToLead}
              onAttachToContact={onAttachToContact}
              visitorId={visitorId}
              leadId={leadId}
              contactId={contactId}
              disabled={waiting || !chatExists || !!getSFObjectFieldsError}
            />
            {
             ((statusInfo.matchToContact) &&
             (<SalesforceInfo
               infoType={contactInfo}
               Fields={salesforceContactFields}
               editInfo={(type) => { onEditOrCreate(type); }}
             />
              ))
            }
            {
                 ((statusInfo.matchToLead) &&
             (<SalesforceInfo
               infoType={LeadInfo}
               Fields={salesforceLeadFields}
               editInfo={(type) => { onEditOrCreate(type); }}
             />
              ))
            }
            {(salesforceAccountFields !== undefined
               && salesforceAccountFields.length > 0) &&
              (<AccountInfo
                salesforceAccountFields={salesforceAccountFields}
              />)
            }
          </div>
          {
            this.state.showForm && (<div>
              <span className={style.formTitle}>{formTitle}</span>
              <CustomForm
                {...rest}
                ifSalesforceLoading={ifSalesforceLoading} salesforceSubmitError={salesforceSubmitError}
                onSubmitSalesforceForm={onSubmitSalesforceForm} onCancelClick={onCancelClick}
              />
            </div>)
          }
        </div>
      </div>);
  }
}

Salesforce.propTypes = {
  chatId: PropTypes.number,
  visitorId: PropTypes.number,
  salesforceAccountFields: PropTypes.arrayOf(PropTypes.shape({
    label: PropTypes.string,
    name: PropTypes.string,
    value: PropTypes.string,
    displayValue: PropTypes.string,
  })),
  salesforceContactFields: PropTypes.arrayOf(PropTypes.shape({
    label: PropTypes.string,
    name: PropTypes.string,
    value: PropTypes.string,
    displayValue: PropTypes.string,
  })),
  salesforceLeadFields: PropTypes.arrayOf(PropTypes.shape({
    label: PropTypes.string,
    name: PropTypes.string,
    value: PropTypes.string,
    displayValue: PropTypes.string,
  })),

  statusInfo: PropTypes.shape({
    matchToContact: PropTypes.bool.isRequired,
    matchToLead: PropTypes.bool.isRequired,
    caseCreated: PropTypes.bool.isRequired,
    taskCreated: PropTypes.bool.isRequired,
    successInfo: PropTypes.string.isRequired,
  }),
  editType: PropTypes.number,
  showForm: PropTypes.bool,
  waiting: PropTypes.bool,
  chatExists: PropTypes.bool,
  errorMessage: PropTypes.string,
  ifShowLoading: PropTypes.bool,
  leadId: PropTypes.string.isRequired,
  contactId: PropTypes.string.isRequired,
  salesforceSubmitError: React.PropTypes.string,
  ifSalesforceLoading: PropTypes.bool,
  salesforcelookUp: PropTypes.func,
  editInfo: PropTypes.func,
  onCreateCase: PropTypes.func,
  onEditOrCreate: PropTypes.func,
  onAttachToLead: PropTypes.func,
  onAttachToContact: PropTypes.func,
  onCancelClick: PropTypes.func,
  onSubmitSalesforceForm: PropTypes.func,
};


export default Salesforce;




