import React from 'react';
import * as _ from 'lodash';
import classnames from 'classnames';
import Link from '../../Link/Link';
import style from './info.css';

class InfoCustomVariables extends React.Component {
  shouldComponentUpdate(nextProps) {
    if (!_.isEqual(this.props.customVariables, nextProps.customVariables) ) {
      return true;
    }
    return false;
  }
  render() {
    const { ifEnableCustomVariables, customVariables } = this.props;
    const CustomVariablesVisible = customVariables.length > 0;
    if (CustomVariablesVisible && ifEnableCustomVariables) {
      return (<div className={classnames(style.Container, style.notEdit)}>
        <div className={style.title}>Custom Variable</div>
        <div className={classnames(style.line, style.customVariable, 'contentMenu')} tabIndex={-1}>
          {customVariables.map(customVariable =>
            <div key={customVariable.name}>
              <span>{customVariable.name}: </span>
              {customVariable.value && customVariable.url &&
                <Link newWindow href={customVariable.url}>{customVariable.value}</Link>
              }
              {customVariable.value && !customVariable.url &&
                <span>{customVariable.value}</span>
              }
            </div>
          )}
        </div>
      </div>);
    }
    return null;
  }
}

InfoCustomVariables.propTypes = {
  ifEnableCustomVariables: React.PropTypes.bool,
  customVariables: React.PropTypes.arrayOf(React.PropTypes.shape({
    name: React.PropTypes.string,
    value: React.PropTypes.string,
    url: React.PropTypes.string,
  })),
};
InfoCustomVariables.defaultProps = {
  ifEnableCustomVariables: false,
};
InfoCustomVariables.examples = {
  'Default Example': {
    description: 'Default Example',
    component:
      <InfoCustomVariables
        ifEnableCustomVariables
        customVariables={[{ name: '111', value: 'aaa', url: '' }, { name: '222', value: 'bbb', url: 'www.baidu.com' }]}
      />,
  },
};

export default InfoCustomVariables;



