import React from 'react';
import classnames from 'classnames';
import RadioButtonStyle from '../RadioButton/RadioButton.css';

/**
 * RadioBox
 */

const RadioBox = props => (
  <div className={props.className}>
    {
      props.radioOption.map((item, index) => (
        <label
          key={index}
          className={classnames(RadioButtonStyle.control, RadioButtonStyle.controlRadio)}
        >
          {item.text || item}
          <input
            type="radio"
            name={props.name}
            value={item.value || item}
            disabled={props.isDisabled ? 'disabled' : false}
            checked={props.input.value === (item.value || item)}
            onChange={(event) => {
              if (props.input && props.input.onChange) {
                props.input.onChange(event.target.value);
              }
            }}
          />
          <div className={RadioButtonStyle.controlIndicator}></div>
          {item.component ? item.component : ''}
        </label>
      ))
    }
  </div>
);

RadioBox.propTypes = {
  input: React.PropTypes.shape({
    value: React.PropTypes.oneOfType([React.PropTypes.number, React.PropTypes.string]).isRequired,
    onChange: React.PropTypes.func.isRequired,
  }).isRequired,
  isDisabled: React.PropTypes.bool,
  name: React.PropTypes.string.isRequired,
  radioOption: React.PropTypes.array.isRequired,
  className: React.PropTypes.string,
};

RadioBox.defaultProps = {
};

RadioBox.examples = {
  DefaultExample: {
    description: 'Default Example',
    component: <RadioBox
      radioOption={['1', '2', '3']}
      input={{ value: '2', onChange: value => console.log(value) }}
      name="test"
    />,
  },
};

export default RadioBox;



