import React from 'react';
import style from './FeatureHelp.css';
import Icon from '../Icon/Icon';
import { icons } from '../../constants/enums';

class FeatureHelp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hide: false,
    };
    this.onClick = this.onClick.bind(this);
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (this.props.text !== nextProps.text ||
    this.props.ifShow !== nextProps.ifShow ||
    this.props.featrueNumber !== nextProps.featrueNumber ||
    this.state.hide !== nextState.hide) {
      return true;
    }
    return false;
  }
  onClick() {
    this.setState({ hide: true });
    this.props.onGotitClick();
  }
  render() {
    const { ifShow, text } = this.props;
    if (ifShow) {
      return (
        <div className={style.Container} style={{ display: this.state.hide ? 'none' : 'flex' }}>
          <div className={style.text}>
            {text}
          </div>
          <span className={style.action} onClick={this.onClick}>
            <Icon type={icons.close} color="#b6924e" size={10} />
          </span>
        </div>);
    }
    return null;
  }
}

FeatureHelp.propTypes = {
  ifShow: React.PropTypes.bool,
  text: React.PropTypes.string,
  featrueNumber: React.PropTypes.number,
  onGotitClick: React.PropTypes.func.isRequired,
};

FeatureHelp.examples = {
  'Default Example': {
    description: 'Default Example',
    component:
      <FeatureHelp
        ifShow text="China" featrueNumber={1} onGotitClick={
        () => { console.log('got it!'); }
      }
      />,
  },
};

export default FeatureHelp;



