import React from 'react';
import _ from 'lodash';
import classnames from 'classnames';
import { icons } from '../../constants/enums';
import Icon from '../Icon/Icon';
import Checkbox from '../Checkbox/Checkbox';
import style from './Form.css';
import memoryStore from '../../services/memoryStore';

const memoryKeyOpened = 'advancewrapupopenedgroup';
const memoryKeySelectedValue = 'advancewrapupselectedValue';
class AdvanceCategory extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      openeds: memoryStore.get(`${memoryKeyOpened}_${props.chatId}`, null, {}),
      values: memoryStore.get(`${memoryKeySelectedValue}_${props.chatId}`, null, []),
    };
    this.clickArrow = this.clickArrow.bind(this);
    this.valueChange = this.valueChange.bind(this);
  }
  componentWillReceiveProps(nextProps) {
    if (this.props.chatId !== nextProps.chatId) {
      const newState = {
        openeds: memoryStore.get(`${memoryKeyOpened}_${nextProps.chatId}`, null, {}),
        values: memoryStore.get(`${memoryKeySelectedValue}_${nextProps.chatId}`, null, []),
      };
      this.setState(newState);
    }
  }
  componentDidUpdate() {
    memoryStore.set(`${memoryKeyOpened}_${this.props.chatId}`, this.state.openeds);
    memoryStore.set(`${memoryKeySelectedValue}_${this.props.chatId}`, this.state.values);
  }
  clickArrow(groupName) {
    const openedGroup = this.state.openeds;
    openedGroup[groupName] = !!this.state.openeds[groupName] === false;
    this.setState({
      openeds: openedGroup,
    });
  }
  valueChange(id) {
    const selectedValues = this.state.values;
    const i = selectedValues.indexOf(id);
    if (i >= 0) {
      selectedValues.splice(i, 1);
    } else {
      selectedValues.push(id);
    }
    this.setState({
      values: selectedValues,
    });
    this.props.valueChange(selectedValues);
  }
  render() {
    const {
      fieldInfo,
      disable,
      meta,
      fieldId,
    } = this.props;
    const { touched, error } = meta;
    const { displayText, ifRequired, groups } = fieldInfo;
    const prepend = (<div id={`error_${fieldId}`} className={style.fieldTitle}>
      {displayText}
      {ifRequired && <span className={style.required}>*</span>}
      {touched && error && <span className={style.required}>{error}</span>}
    </div>);
    return (
      <div className={style.field}>
        {prepend}
        {
          groups.map((grp) => {
            const groupName = `${fieldId}_${grp.id}`;
            return (
              <div key={groupName} className={style.groupsection}>
                <div
                  className={classnames(style.groupdesc,
                             this.state.openeds[groupName] && style.opened)}
                  onClick={() => this.clickArrow(groupName)}
                >
                  <div className={style.arrow}>
                    <Icon
                      type={icons.arrowDown}
                      size={14}
                    />
                  </div>
                  <span className={style.groupdesc}>{grp.displayText}</span>
                </div>
                {this.state.openeds[groupName] && grp.options.map(opt => (
                  <Checkbox
                    key={opt.id}
                    input={{
                      value: this.state.values.indexOf(opt.id) >= 0,
                      onChange: val => this.valueChange(opt.id, val),
                    }}
                    text={opt.displayText}
                    isDisabled={disable}
                    initialValue={false}
                    className={style.item}
                  />
                ))}
              </div>
            );
          })
        }
      </div>
    );
  }
}

AdvanceCategory.propTypes = {
  chatId: React.PropTypes.number,
  fieldInfo: React.PropTypes.shape({
    displayText: React.PropTypes.string,
    ifRequired: React.PropTypes.bool,
    groups: React.PropTypes.array,
    type: React.PropTypes.number,
  }).isRequired,
  disable: React.PropTypes.bool,
  fieldId: React.PropTypes.number,
  meta: React.PropTypes.shape({}),
  valueChange: React.PropTypes.func.isRequired,
};

export default AdvanceCategory;



