import React, { PropTypes, Component } from 'react';
import classnames from 'classnames';
import Link from '../../Link/Link';
import style from './navigation.css';
import { formatTimeWithMillSecondKeep2Unit } from '../../../utils/time';


class NavigationFirstItem extends Component {

  constructor(props) {
    super(props);
    this.timer = null;
    this.state = {
      currentTimeInterval:
       ((new Date() - props.time) - (props.timezoneoffset * 1000)),
    };
  }


  componentDidMount() {
    if (this.timer !== null) {
      clearInterval(this.timer);
    }

    this.timer = setInterval(() => {
      this.setState({
        currentTimeInterval: (new Date() - this.props.time)
          - (this.props.timezoneoffset * 1000),
      });
    }, 1000);
  }
  componentWillUnmount() {
    clearInterval(this.timer);
  }



  render() {
    const {
      pageURL,
      title,
  } = this.props;

    return (
      <div
        data-tip={pageURL}
        data-for="visitUrl"
        className={style.infoline}
      >
        <span className={style.info}>
          <span className={classnames(style.title, style.withCurrent)}>
            <Link
              className={style.preview}
              href={pageURL}
              newWindow
            >
              {title}
            </Link>
          </span>
          {<span className={style.current}>(Current Page)</span>}
        </span>
        <span className={style.date}>{formatTimeWithMillSecondKeep2Unit(
          this.state.currentTimeInterval
        )}</span>
      </div>
    );
  }}

NavigationFirstItem.propTypes = {
  pageURL: PropTypes.string.isRequired,
  time: PropTypes.instanceOf(Date).isRequired,
  title: PropTypes.string.isRequired,
  timezoneoffset: PropTypes.number,
};

NavigationFirstItem.examples = {
  'Default Example': {
    description: 'NavigationItem',
    component:
      <NavigationFirstItem isCurrentPage pageURL="http://www.comm100.com" time="6h 30m" />,
  },
};


export default NavigationFirstItem;



