import React from 'react';
import TextArea from '../TextArea/TextArea';
import RadioBox from '../RadioBox/RadioBox';
import Input from '../Input/Input';
import Checkbox from '../Checkbox/Checkbox';
import CheckboxList from '../Checkbox/CheckboxList';
import Select from '../Select/Select';
import * as fieldType from '../../constants/enums/fieldType';
import style from './Form.css';

const RenderField = (props) => {
  const { input, fieldInfo, disable, meta, name, ...others } = props;
  const { touched, error } = meta;
  const { type, displayText, ifRequired, options } = fieldInfo;
  const prepend = (<div id={`error_${name}`} className={style.fieldTitle}>
    {displayText}
    {ifRequired && <span className={style.required}>*</span>}
    {touched && error && <span className={style.required}>{error}</span>}
  </div>);
  if (type === fieldType.Text) {
    return (
      <div className={style.field}>
        {prepend}
        <Input
          maxLength={512}
          type="text"
          isDisabled={disable}
          className={style.inputWidth}
          input={input} meta={meta}
          {...others}
        />
      </div>
    );
  } else if (type === fieldType.TextArea || type === fieldType.WrapupComment) {
    return (
      <div className={style.field}>
        {prepend}
        <TextArea
          rows="5"
          maxLength={512}
          type="text"
          isDisabled={disable}
          className={style.inputWidth}
          input={input}
          meta={meta}
          {...others}
        />
      </div>
    );
  } else if (type === fieldType.Radio) {
    return (
      <div className={style.field}>
        {prepend}
        <RadioBox
          name={name}
          className={style.radio}
          isDisabled={disable}
          radioOption={options.map(item => ({ text: item.displayText, value: item.id.toString() }))}
          input={input} {...others}
        />
      </div>
    );
  } else if (type === fieldType.CheckBox) {
    return (
      <div className={style.field}>
        {prepend}
        <Checkbox input={input} {...others} isDisabled={disable} />
      </div>
    );
  } else if (type === fieldType.DropDownList) {
    const defaultSelectOption = [{ text: ' -select an option- ', value: '' }];
    const optionArray = defaultSelectOption.concat(options.map(item => ({ text: item.displayText, value: item.id.toString() })));
    return (
      <div className={style.field}>
        {prepend}
        <Select
          input={input}
          isDisabled={disable}
          options={optionArray}
        />
      </div>
    );
  } else if (type === fieldType.WrapupCategory) {
    const defaultSelectOption = [{ text: ' -select an option- ', value: -1 }];
    const optionArray = defaultSelectOption.concat(options.map(item => ({ text: item.displayText, value: item.id })));
    return (
      <div className={style.field}>
        {prepend}
        <Select
          input={input}
          isDisabled={disable}
          options={optionArray}
        />
      </div>
    );
  } else if (type === fieldType.CheckBoxList) {
    return (
      <div className={style.field}>
        {prepend}
        <CheckboxList
          options={options.map(item => ({ id: item.id.toString(), text: item.displayText }))}
          input={input} {...others}
          isDisabled={disable}
        />
      </div>
    );
  }
  return null;
};

RenderField.propTypes = {
  fieldInfo: React.PropTypes.shape({
    displayText: React.PropTypes.string,
    ifRequired: React.PropTypes.bool,
    options: React.PropTypes.array,
    type: React.PropTypes.number,
  }).isRequired,
};


export default RenderField;



