import React, { PropTypes, Component } from 'react';
import classnames from 'classnames';
import ReactTooltip from 'react-tooltip';
import Link from '../../Link/Link';
import RadioButton from '../../RadioButton/RadioButton';
import { historyType, icons } from '../../../constants/enums';
import agentTabStyle from '../AgentTab.css';
import style from './visitorHistory.css';
import HistoryChatDetals from './VisitorHistoryChatDetails';
import HistoryMessageDetals from './VisitorHistoryMessageDetails';
import Icon from '../../../components/Icon/Icon';
import { shouldComponentUpdateGen } from '../../../utils/helper';
import { isNullOrEmpty } from '../../../utils/str';
import { gotoVisitorChatDetail } from '../../../containers/Business/common';
import commonStyle from '../../CommonResources/common.css';


const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'VisitorHistory',
  ['visitorId', 'historyMessageId', 'historyChatId',
    'historyRefreshing', 'historyViewType', 'isViewAllItems', 'ifUseMainServer',
    'historyChatDetailRefreshing', 'historyMessageDetailRefreshing',
    // 'chatItems', 'messageItems', 'offLineMessage', 'chatInfo',
  ]
);


class VisitorHistory extends Component {

  constructor(props) {
    super(props);
    this.gotoVisitorChatDetail = this.gotoVisitorChatDetail.bind(this);
  }

  componentDidMount() {
    /**
     * @todo is it required to call, even when `historyViewType` is chat already?
     * if history has been loaded from server, re-selecting History tab will causing rendering
     * twice. One of them caused by tab changing and one of them caused by following lin.
     * Seems unnecessary.
     */
    this.historyViewTypeChanged(historyType.chat);
    if (this.props.chatItems === undefined) {
      this.getHistoryLists();
    }
  }

  shouldComponentUpdate(nextProps) {
    return shouldComponentUpdateHelper(this.props, nextProps);
  }

  componentDidUpdate(prevProps) {
    if (prevProps.visitorId !== this.props.visitorId) {
      this.historyViewTypeChanged(historyType.chat);
    }
    if (prevProps.visitorId !== this.props.visitorId
    && ((this.props.historyViewType === historyType.chat &&
    this.props.chatItems === undefined)
    || (this.props.historyViewType === historyType.offlineMessage &&
    this.props.messageItems === undefined))) {
      this.getHistoryLists();
    } else if (prevProps.historyChatId !== this.props.historyChatId
     && this.props.historyChatId !== 0
     && this.props.chatInfo === undefined) {
      this.props.getChatHistoryDetails(
        this.props.visitorId,
        this.props.historyChatId,
        historyType.chat);
    } else if (prevProps.historyMessageId !== this.props.historyMessageId
     && this.props.historyMessageId !== 0
     && this.props.offLineMessage === undefined) {
      this.props.getChatHistoryDetails(
        this.props.visitorId,
        this.props.historyMessageId,
        historyType.offlineMessage);
    }
  }


  getHistoryLists(...others) {
    let historyViewType = this.props.historyViewType;
    if (others.length > 0) {
      historyViewType = others[0];
    }
    this.props.refreshHistory(
      this.props.visitorId,
      historyViewType
      );
  }

  getHistoryDetails(itemId) {
    this.props.refreshHistoryDetail(
      this.props.visitorId,
      itemId,
      this.props.historyViewType
      );
  }

  updateViewAllStatus() {
    if (this.props.historyViewType === historyType.chat) {
      this.props.updateIsViewAllChats(this.props.visitorId, true);
    } else if (this.props.historyViewType === historyType.offlineMessage) {
      this.props.updateIsViewAllMessages(this.props.visitorId, true);
    }
  }

  historyItemClicked(id, forceUpdate = false) {
    if (this.props.historyViewType === historyType.chat) {
      if (this.props.chatInfo === undefined ||
        this.props.historyChatId !== id || forceUpdate) {
        this.getHistoryDetails(id);
      } else {
        this.props.updateHistoryChatId(this.props.visitorId, 0);
      }
    } else if (this.props.offLineMessage === undefined ||
        this.props.historyMessageId !== id || forceUpdate) {
      this.getHistoryDetails(id);
    } else {
      this.props.updateHistoryMessageId(this.props.visitorId, 0);
    }
  }

  gotoVisitorChatDetail(purchaseUrl) {
    gotoVisitorChatDetail({
      purchaseUrl,
      loginUrl: this.props.loginUrl,
      autoLoginUrl: this.props.autoLoginUrl });
  }

  historyViewTypeChanged(value) {
    this.props.updateHistoryViewType(this.props.visitorId, value);
    if (value === historyType.chat) {
      if (this.props.chatItems === undefined) {
        this.getHistoryLists(value);
      }
    } else if (this.props.messageItems === undefined) {
      this.getHistoryLists(value);
    }
  }

  render() {
    return (
      <div className={style.history}>
        {
            this.props.historyRefreshing &&
            (<div className={style.infoline}>
              <div className={agentTabStyle.loading}>Loading...</div>
            </div>)
        }

        {!this.props.historyRefreshing &&
            (<div>
              <div className={style.infoline}>
                <Link
                  onClick={() => {
                    this.getHistoryLists();
                  }
                 }
                  className={agentTabStyle.refresh}
                >
                Refresh
                </Link>
              </div>
              <div className={classnames(style.infoline, style.historyType)}>
                <RadioButton
                  radioOption={
                    [{ value: historyType.chat.toString(),
                      text: 'Chats',
                      checked: this.props.historyViewType === historyType.chat,
                    }, {
                      value: historyType.offlineMessage.toString(),
                      text: 'Messages',
                      checked: this.props.historyViewType === historyType.offlineMessage,
                    }]
                      }
                  // defaultVal={this.props.historyViewType.toString()}
                  handleChange={(event) => {
                    this.historyViewTypeChanged(+(event.target.value));
                  }}
                  name="Chats_Messages"
                  horizontal
                />
              </div>
            </div>
            )
          }

        {this.props.historyViewType === historyType.chat && (<div>
          {
          (!this.props.historyRefreshing) &&
          ((!this.props.ifUseMainServer) &&
          (<div className={style.infoline}>
          Live Chat is running on the secondary server.
          We're sorry but you're unable to get chat records.
          </div>))
          }

          {(!this.props.historyRefreshing) && (this.props.ifUseMainServer) &&
          (<div>

            {((this.props.chatItems === undefined
          || this.props.chatItems.length === 0) &&
          isNullOrEmpty(this.props.errorInfo.getChatHistoryError) &&
          (<div className={style.infoline}>
          This visitor has no chat records yet.
          </div>)
          )}

            { (!isNullOrEmpty(this.props.errorInfo.getChatHistoryError)) &&
           (<div>
             <div className={style.detailError}> {this.props.errorInfo.getChatHistoryError} </div>
           </div>)}

            { (isNullOrEmpty(this.props.errorInfo.getChatHistoryError) &&
             (this.props.chatItems !== undefined
              && this.props.chatItems.length > 0)) &&
              (this.props.chatItems.map(item =>
              (
                <div id={`chat_${item.chatId}`} key={`visitorHistoryChat_${item.chatId}`}>
                  <div className={style.historyItem} data-tip={item.operator} data-for="historyTitle">
                    <Link
                      href={`#chat_${item.chatId}`}
                      onClick={() => {
                        this.historyItemClicked(
                         item.chatId);
                      }}
                      className={style.listItem}
                    >
                      {item.time}&nbsp;&nbsp;&nbsp;&nbsp;{item.operator}
                    </Link>
                  </div>

                  {(item.chatId === this.props.historyChatId) && (
                    <div style={{ position: 'relative' }}>


                      {(isNullOrEmpty(this.props.errorInfo.getChatHistoryDetailsError)) &&
                      (<HistoryChatDetals
                        refreshing={this.props.historyChatDetailRefreshing}
                        chatInfo={this.props.chatInfo}
                        gotoVisitorChatDetail={this.gotoVisitorChatDetail}
                        ifEnableWrapupChat={this.props.ifEnableWrapupChat}
                      />)}

                      {(!isNullOrEmpty(this.props.errorInfo.getChatHistoryDetailsError)) &&
                      (<div id="" className={style.detailError}>
                        <span>
                          {this.props.errorInfo.getChatHistoryDetailsError}
                        </span>
                        <Link
                          onClick={() => {
                            this.props.getChatHistoryDetails(
                              this.props.visitorId,
                              item.chatId,
                              historyType.chat);
                          }}
                          className={style.detailErrorRrefresh}
                        >Refresh</Link>
                      </div>
                    )}

                    </div>
                  )}
                </div>
              ))
          )}
          </div>)}

        </div>)
        }
        {(this.props.historyViewType === historyType.offlineMessage) && (<div>

          {
          (!this.props.historyRefreshing) &&
          ((!this.props.ifUseMainServer) &&
          (<div className={style.infoline}>
          Live Chat is running on the secondary server.
          We're sorry but you're unable to get chat records.
          </div>))
          }


            {(!this.props.historyRefreshing) && (this.props.ifUseMainServer) &&
          (<div>
            {((this.props.messageItems === undefined
          || this.props.messageItems.length === 0) &&
          isNullOrEmpty(this.props.errorInfo.getOfflineMessageHistoryError) &&
          (<div className={style.infoline}>
          This visitor has no message records yet.
          </div>)
          )}


            { (!isNullOrEmpty(this.props.errorInfo.getOfflineMessageHistoryError)) &&
           (<div>
             <div className={style.detailError}>
               {this.props.errorInfo.getOfflineMessageHistoryError}
             </div>
           </div>)}

            {(this.props.messageItems !== undefined
          && this.props.messageItems.length > 0) &&
          isNullOrEmpty(this.props.errorInfo.getOfflineMessageHistoryError) &&
          (this.props.messageItems.map(item =>
              (<div id={`message_${item.messageId}`} key={`visitorHistoryMessage_${item.messageId}`}>
                <div className={style.historyItem}>
                  <Link
                    href={`#message_${item.messageId}`}
                    onClick={() => {
                      this.historyItemClicked(
                         item.messageId);
                    }}
                    className={style.listItem}
                  >Message at {item.time}
                  </Link>
                </div>
                {item.messageId === this.props.historyMessageId &&
                 (<div style={{ position: 'relative' }}>

                   {(isNullOrEmpty(this.props.errorInfo.getOfflineMessageDetailsError)) &&
                      (<HistoryMessageDetals
                        refreshing={this.props.historyMessageDetailRefreshing}
                        offLineMessage={this.props.offLineMessage}
                      />)}

                   {(!isNullOrEmpty(this.props.errorInfo.getOfflineMessageDetailsError)) &&
                      (<div className={style.detailError}>
                        <span>
                          {this.props.errorInfo.getOfflineMessageDetailsError}
                        </span>
                        <Link
                          onClick={() => {
                            this.props.getChatHistoryDetails(this.props.visitorId,
                              item.messageId,
                              historyType.offlineMessage
                              );
                          }
                        }
                          className={style.detailErrorRrefresh}
                        >Refresh</Link>
                      </div>
                    )}
                 </div>)}
              </div>
          )))}

          </div>)}
        </div>)}

        {!this.props.isViewAllItems &&
        <div className={style.moreChat}>
          <Link
            onClick={() => { this.updateViewAllStatus(); }}
          >
            <Icon type={icons.arrowDown} />
            <div className={style.moreText}>MORE</div>
          </Link>
        </div>
        }

        <ReactTooltip
          id="historyTitle"
          effect="solid"
          place="bottom"
          multiline
          class={classnames(style.tooltip, commonStyle.tooltipInfo)}
        />
      </div>);
  }
}

VisitorHistory.propTypes = {
  loginUrl: PropTypes.string.isRequired,
  autoLoginUrl: PropTypes.string.isRequired,
  chatItems: PropTypes.arrayOf(PropTypes.shape({
    time: PropTypes.string.isRequired,
    operator: PropTypes.string.isRequired,
    chatId: PropTypes.number,
  }).isRequired,
   ),

  messageItems: PropTypes.arrayOf(PropTypes.shape({
    time: PropTypes.string.isRequired,
    chatId: PropTypes.number,
    messageId: PropTypes.number.isRequired,
  }).isRequired,
  ),

  offLineMessage: PropTypes.shape({
    messageId: PropTypes.number,
    content: PropTypes.string,
    customVariables: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    })).isRequired,
  }),
  chatInfo: PropTypes.shape({
    id: PropTypes.number,
    detailUrl: PropTypes.string,
    chatId: PropTypes.number,
    customVariables: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    })).isRequired,
    chatContent: PropTypes.arrayOf(PropTypes.shape({
      id: PropTypes.number,
      senderName: PropTypes.string,
      sendertype: PropTypes.number,
      infoPrefix: PropTypes.string,
      sendMessageTime: PropTypes.string,
      endTime: PropTypes.string,
      info: PropTypes.string,
    })).isRequired,

    pre_chat_ser: PropTypes.arrayOf(PropTypes.shape({
      id: PropTypes.number.isRequired,
      name: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    }).isRequired),
    pre_chat: PropTypes.arrayOf(PropTypes.shape({
      id: PropTypes.number.isRequired,
      name: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    }).isRequired),
    wrap_up: PropTypes.arrayOf(PropTypes.shape({
      id: PropTypes.number.isRequired,
      name: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    })).isRequired,
    agentComment: PropTypes.shape({
      name: PropTypes.string.isRquired,
      value: PropTypes.string.isRquired,
    }).isRquired,
    chatCategory: PropTypes.shape({
      name: PropTypes.string.isRquired,
      value: PropTypes.string.isRquired,
    }).isRquired,
  }),
  errorInfo: PropTypes.shape({
    getChatHistoryError: PropTypes.string,
    getOfflineMessageHistoryError: PropTypes.string,
    getChatHistoryDetailsError: PropTypes.string,
    getOfflineMessageDetailsError: PropTypes.string,
  }),


  visitorId: PropTypes.number.isRequired,
  historyMessageId: PropTypes.number.isRequired,
  historyChatId: PropTypes.number.isRequired,
  historyRefreshing: PropTypes.bool.isRequired,
  historyChatDetailRefreshing: PropTypes.bool.isRequired,
  historyMessageDetailRefreshing: PropTypes.bool.isRequired,
  historyViewType: PropTypes.oneOf([
    historyType.chat,
    historyType.offlineMessage,
    historyType.visits]),
  isViewAllItems: PropTypes.bool.isRequired,
  ifUseMainServer: PropTypes.bool.isRequired,

  refreshHistory: PropTypes.func,
  refreshHistoryDetail: PropTypes.func,
  updateHistoryMessageId: PropTypes.func,
  updateHistoryChatId: PropTypes.func,
  getChatHistoryDetails: PropTypes.func,
  updateHistoryViewType: PropTypes.func,
  updateIsViewAllChats: PropTypes.func,
  updateIsViewAllMessages: PropTypes.func,

};


// VisitorHistory.examples = {
