import React from 'react';
import classnames from 'classnames';
import style from './Wrapup.css';
import Link from '../../Link/Link';
import { gotoSpecificUrl } from '../../../containers/Business/common';

class WrapupDetail extends React.Component {
  constructor(props) {
    super(props);
    this.gotoWrapupDetailUrl = this.gotoWrapupDetailUrl.bind(this);
  }
  shouldComponentUpdate(nextProps) {
    if (this.props.id !== nextProps.id ||
    this.props.siteId !== nextProps.siteId ||
    this.props.sessionId !== nextProps.sessionId ||
    this.props.ifOpen !== nextProps.ifOpen ||
    this.props.ifWrapupDetailsLoading !== nextProps.ifWrapupDetailsLoading ||
    this.props.sessionType !== nextProps.sessionType) {
      return true;
    }
    return false;
  }
  gotoWrapupDetailUrl(wrapupDetailUrl) {
    gotoSpecificUrl(false, wrapupDetailUrl, this.props.loginUrl, this.props.autoLoginUrl);
  }
  render() {
    const { ifOpen, className, wrapupContent, siteId, id, sessionType, sessionId,
      liveChatReportUrl, ifWrapupDetailsLoading } = this.props;
    const wrapupDetailUrl = `${liveChatReportUrl}/TranscriptsDetailsWithoutBase.aspx?date=${id}_${sessionType}_${sessionId}&siteId=${siteId}`;
    return (<div className={style.relative}>
      {
        ifWrapupDetailsLoading && ifOpen &&
        <div className={style.Loading}>
          Loading...
        </div>
      }
      { !ifWrapupDetailsLoading && wrapupContent && wrapupContent.length > 0 &&
      <div
        style={{ display: ifOpen ? 'block' : 'none' }}
        className={classnames(className, style.WrapupDetailContainer)}
      >
        <div className={style.detailsContent}>
          <div className={style.WrapupDetailsHeader}>
            <h4>Wrap-up Info</h4>
            <Link
              onClick={(event) => {
                event.preventDefault();
                event.stopPropagation();
                this.gotoWrapupDetailUrl(wrapupDetailUrl);
              }}
              className={style.detailsLink}
            >Details</Link>
          </div>
          <div className={classnames(style.WrapupDetailContent, 'contentMenu')}>
            {wrapupContent.map(item => {
              if (item.split('\n').length > 0) {
                return item.split('\n').map(line => {
                  return (<div>{line}</div>);
                });
              }
              return (<div>{item}</div>);
            })}
          </div>
        </div>
      </div>
      }
    </div>);
  }
}

WrapupDetail.propTypes = {
  className: React.PropTypes.string,
  siteId: React.PropTypes.number,
  id: React.PropTypes.number,
  sessionType: React.PropTypes.number,
  sessionId: React.PropTypes.number,
  wrapupContent: React.PropTypes.array,
  liveChatReportUrl: React.PropTypes.string,
  ifWrapupDetailsLoading: React.PropTypes.bool,
  ifOpen: React.PropTypes.bool,
};

export default WrapupDetail;



