import * as visitorDetails from '../constants/types/visitorDetails';

export const handleNavigationRefreshing = visitorId => ({
  type: visitorDetails.navigationUpdate,
  payload: visitorId,
});

export const handleUpdateIfNavigationLoading = status => ({
  type: visitorDetails.updateIfNavigationLoading,
  payload: status,
});




