import React from 'react';
import ReactTooltip from 'react-tooltip';
import classnames from 'classnames';
import Os from '../../Icons/OS';
import Browser from '../../Icons/Browser';
import commonStyle from '../../CommonResources/common.css';
import style from './info.css';

class OsBrowserImage extends React.Component {
  shouldComponentUpdate(nextProps) {
    if (this.props.browser !== nextProps.browser ||
    this.props.operatingSystem !== nextProps.operatingSystem ||
    this.props.ifOsBrowserImageShow !== nextProps.ifOsBrowserImageShow) {
      return true;
    }
    return false;
  }
  render() {
    const { browser, operatingSystem, ifOsBrowserImageShow } = this.props;
    if (ifOsBrowserImageShow) {
      return (<div className={classnames(style.Container, style.notEdit)}>
        <div className={classnames(style.osIconWrapper, commonStyle.clearfix)}>
          <Os name={operatingSystem} />
          <Browser name={browser} />
        </div>
        <ReactTooltip id="chatContainer" effect="solid" place="bottom" class={style.tooltip} />
      </div>);
    }
    return null;
  }
}

OsBrowserImage.propTypes = {
  ifOsBrowserImageShow: React.PropTypes.bool,
  browser: React.PropTypes.string,
  operatingSystem: React.PropTypes.string,
};

OsBrowserImage.examples = {
  'Default Example': {
    description: 'Default Example',
    component:
      <OsBrowserImage ifOsBrowserImageShow browser="Google Chrome 54.0.2840.71" operatingSystem="Windows 10" />,
  },
};

export default OsBrowserImage;



