import React from 'react';
import ReactTooltip from 'react-tooltip';
import classnames from 'classnames';
import style from './info.css';

const getDeviceInfo = (language, screenResolution, timezone, flashVersion) => {
  let result = '';
  if (language) {
    result += `${language}, `;
  }
  if (screenResolution) {
    result += `${screenResolution}, `;
  }
  if (timezone) {
    result += `${timezone}, `;
  }
  if (flashVersion) {
    result += `Flash ${flashVersion}, `;
  }
  if (result.length > 0) {
    result = result.substring(0, result.length - 2);
  }
  return result;
};

const getDeviceInfoTooltip = (language, screenResolution, timezone, flashVersion) => {
  let result = '';
  if (language) {
    result += `Language: ${language}<br>`;
  }
  if (screenResolution) {
    result += `Screen Resolution: ${screenResolution}<br>`;
  }
  if (timezone) {
    result += `Time Zone: ${timezone}<br>`;
  }
  if (flashVersion) {
    result += `Flash Version: ${flashVersion}<br>`;
  }
  if (result.length > 0) {
    result = result.substring(0, result.length - 4);
  }
  return result;
};

class DeviceInfo extends React.Component {
  shouldComponentUpdate(nextProps) {
    if (this.props.language !== nextProps.language ||
      this.props.screenResolution !== nextProps.screenResolution ||
      this.props.timezone !== nextProps.timezone ||
      this.props.flashVersion !== nextProps.flashVersion) {
      return true;
    }
    return false;
  }
  render() {
    const { language, screenResolution, timezone, flashVersion }=this.props;
    return (
      <div className={classnames(style.Container, style.notEdit)}>
        <div className={style.title}>Device Info</div>
        <div className={classnames(style.line, 'contentMenu')} tabIndex={-1}>
          <span
            className={style.inlineBlock}
            data-tip={getDeviceInfoTooltip(language, screenResolution, timezone, flashVersion)}
            data-for="DeviceInfo"
          >
            {getDeviceInfo(language, screenResolution, timezone, flashVersion)}
          </span>
        </div>
        <ReactTooltip
          id="DeviceInfo"
          effect="solid"
          place="bottom"
          multiline
          class={style.tooltip}
        />
      </div>);
  }
}

DeviceInfo.propTypes = {
  language: React.PropTypes.string,
  screenResolution: React.PropTypes.string,
  timezone: React.PropTypes.string,
  flashVersion: React.PropTypes.string,
};

DeviceInfo.examples = {
  'Default Example': {
    description: 'Default Example',
    component:
      <DeviceInfo
        language="en-US" screenResolution="1680x1050"
        timezone="GMT +08:00" flashVersion="23.0.0"
      />,
  },
};

export default DeviceInfo;



