import React from 'react';
import classnames from 'classnames';
import ReactTooltip from 'react-tooltip';
import * as _ from 'lodash';
import { icons } from '../../../constants/enums';
import Icon from '../../Icon/Icon';
import IconInput from '../../Input/IconInput';
import Button from '../../Button/Button';
import style from './info.css';
import * as str from '../../../utils/str';
import * as localStorage from '../../../utils/localStorage';
import Select from '../../Select/Select';

const validate = (values) => {
  const errors = {};
  if (!values.name) {
    errors.name = 'Name is Required!';
    errors._error = 'Name is Required!';
  }
  if (values.email && !str.isEmail(values.email)) {
    errors.email = 'Email is invalid!';
    errors._error = 'Email is invalid!';
  }
  return errors;
};

class InfoPrechatForm extends React.Component {
  constructor(props) {
    super(props);
    const cacheState = localStorage.getObject('prechatFormState');
    if (cacheState) {
      this.state = cacheState;
    } else {
      this.state = {
        values: props.initValues,
        errors: {},
      };
    }

    this.handleValueChange = this.handleValueChange.bind(this);
    this.onSubmitPreChat = this.onSubmitPreChat.bind(this);
    this.onCancelClick = this.onCancelClick.bind(this);
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (this.props.ifpreChatLoading !== nextProps.ifpreChatLoading
      || !_.isEqual(this.state.values, nextState.values)
      || !_.isEqual(this.state.errors, nextState.errors)) {
      return true;
    }
    return false;
  }
  componentDidUpdate(prevProps, prevState) {
    if (!_.isEqual(this.state.values, prevState.values)
      || !_.isEqual(this.state.errors, prevState.errors)) {
      localStorage.setObject('prechatFormState', this.state);
    }
    if (this.state.errors._error) {
      const errorElement = this.errorMessageDiv;
      if (errorElement) {
        if (errorElement.scrollIntoViewIfNeeded) errorElement.scrollIntoViewIfNeeded();
        else errorElement.scrollIntoView();
      }
    }
  }
  onSubmitPreChat() {
    const e = validate(this.state.values);
    if (Object.keys(e).length === 0) {
      this.props.handleSubmitpreChat(this.state.values);
    }
    this.setState({
      errors: e,
    });
  }
  onCancelClick() {
    localStorage.deleteItem('prechatFormState');
    this.props.onPreChatCancelClick();
  }
  handleValueChange(name, value) {
    const e = Object.assign({}, this.state.errors);
    if (e[name]) {
      e[name] = '';
    }
    e._error = '';
    const temp = Object.assign({}, this.state.values);
    temp[name] = value;
    if (!_.isEqual(temp, this.state.values)) {
      this.setState({
        values: temp,
        errors: e,
      });
    }
  }
  render() {
    const departments = this.props.departments.map(
      ({ id, name }) => ({ text: name, value: id })
    );
    const defaultSelectOption = { text: ' --Select a department-- ', value: -1 };
    return (
      <div className={style.formWrapper}>
        <div ref={(element) => { this.errorMessageDiv = element; }} className={style.errorMessage} style={{ display: this.state.errors._error ? 'block' : 'none' }}>
          <span>{this.state.errors._error}</span>
        </div>
        <div>
          <div className={style.line}>
            <IconInput
              name="name"
              className={style.input}
              icon={<Icon data-tip="Name" data-for="prechatTooltip" type={icons.loginUser} color="#aaa" size="small" />}
              placeholder="Name"
              type="text"
              maxLength={64}
              input={{ value: this.state.values.name || '',
                onChange: e => this.handleValueChange('name', e.target.value) }}
              meta={{ touched: true, invalid: this.state.errors.name ? true : false, error: this.state.errors.name || '' }}
            />
          </div>
          <div className={style.line}>
            <IconInput
              name="email"
              className={style.input}
              icon={<Icon data-tip="Email" data-for="prechatTooltip" type={icons.sendEmail} color="#aaa" size="small" />}
              placeholder="Email"
              type="email"
              maxLength={64}
              input={{ value: this.state.values.email || '',
                onChange: e => this.handleValueChange('email', e.target.value) }}
              meta={{ touched: true, invalid: this.state.errors.email ? true : false, error: this.state.errors.email || '' }}
            />
          </div>
          <div className={style.line}>
            <IconInput
              name="phone"
              className={style.input}
              icon={<Icon data-tip="Phone" data-for="prechatTooltip" type={icons.waitting} color="#aaa" size="small" />}
              placeholder="Phone"
              type="tel"
              maxLength={32}
              input={{ value: this.state.values.phone || '',
                onChange: e => this.handleValueChange('phone', e.target.value) }}
              meta={{ touched: true, invalid: this.state.errors.phone ? true : false, error: this.state.errors.phone || '' }}
            />
          </div>
          <div className={style.line}>
            <IconInput
              name="company"
              className={style.input}
              icon={<Icon data-tip="Company" data-for="prechatTooltip" type={icons.company} color="#aaa" size="small" />}
              placeholder="Company"
              type="text"
              maxLength={64}
              input={{ value: this.state.values.company || '',
                onChange: e => this.handleValueChange('company', e.target.value) }}
              meta={{ touched: true, invalid: this.state.errors.company ? true : false, error: this.state.errors.company || '' }}
            />
          </div>
          <div className={style.line}>
            <IconInput
              name="productService"
              className={style.input}
              icon={<Icon data-tip="Product and Service" data-for="prechatTooltip" type={icons.productService} color="#aaa" size="small" />}
              placeholder="Product Service"
              type="text"
              maxLength={256}
              input={{ value: this.state.values.productService || '',
                onChange: e => this.handleValueChange('productService', e.target.value) }}
              meta={{ touched: true, invalid: this.state.errors.productService ? true : false, error: this.state.errors.productService || '' }}
            />
          </div>
          {this.props.ifDepartmentEnabled &&
            <div className={style.line}>
              <div className={style.preIConWrapper}>
                <Icon data-tip="Department" data-for="prechatTooltip" type={icons.department} color="#aaa" size={20} />
              </div>
              <div className={style.select}>
                <Select
                  input={{ value: this.state.values.departmentId || -1,
                    onChange: value => this.handleValueChange('departmentId', value) }}
                  options={[defaultSelectOption, ...departments]}
                />
              </div>
            </div>
          }
          <div className={classnames(style.buttonWrapper, style.preInfoEdit)}>
            <Button
              className={style.okButton}
              onClick={this.onSubmitPreChat}
              disabled={this.props.ifpreChatLoading || _.isEqual(this.state.values, this.props.initValues)}
              loading={this.props.ifpreChatLoading}
              iconPosition="right"
              text="OK"
              type="primary"
              htmlType="button"
            />
            <Button
              onClick={this.onCancelClick}
              text="Cancel"
              htmlType="button"
            />
          </div>
        </div>
        <ReactTooltip id="prechatTooltip" effect="solid" place="bottom" class={style.tooltip} />
      </div>
    );
  }
}

InfoPrechatForm.propTypes = {
  initValues: React.PropTypes.object,
  ifDepartmentEnabled: React.PropTypes.bool,
  departments: React.PropTypes.array,
  handleSubmitpreChat: React.PropTypes.func,
  onPreChatCancelClick: React.PropTypes.func,
  ifpreChatLoading: React.PropTypes.bool,
};


export default InfoPrechatForm;



