import React from 'react';
import classNames from 'classnames';
import * as _ from 'lodash';
import TextArea from '../../TextArea/TextArea';
import Input from '../../Input/Input';
import Checkbox from '../../Checkbox/Checkbox';
import Select from '../../Select/Select';
import DatePicker from '../../DatePicker/DatePicker';
import FormTransfer from '../../Transfer/FormTransfer';
import { fieldType } from '../../../constants/salesforce';
import style from './salesforce.css';
import { getDisplayText, isNeedSelector } from './util';
import Lookup from '../../Modal/SalesforceLookup/SalesforceLookupModel';

class RenderField extends React.Component {
  shouldComponentUpdate(nextProps) {
    if (!_.isEqual(this.props.meta, nextProps.meta) || this.props.input.value !== nextProps.input.value) {
      return true;
    }
    return false;
  }
  render() {
    const props = this.props;
    const { input, fieldInfo, disable, meta, ...others } = props;
    const { touched, error } = meta;
    const { type, label, required, picklistValues, systemName } = fieldInfo;
    const prepend = (<div id={`error_${systemName}`} className={style.fieldName}>
      {getDisplayText(fieldInfo)}
      {required && <span className={style.required}>*</span>}
      {touched && error && <div className={style.required}>{error}</div>}
    </div>);
    if (type === fieldType.percent || type === fieldType.currency || type === fieldType.email ||
      type === fieldType.phone || type === fieldType.text || type === fieldType.double ||
      type === fieldType.string || type === fieldType.id || type === fieldType.int) {
      return (
        <div className={style.field}>
          {prepend}
          <Input
            type="text"
            isDisabled={disable}
            className={classNames(style.inputWidth, style.input)}
            input={input} meta={meta}
            {...others}
          />
        </div>
      );
    } else if (type === fieldType.textarea) {
      return (
        <div className={style.field}>
          {prepend}
          <TextArea
            rows="4"
            type="text"
            isDisabled={disable}
            className={classNames(style.inputWidth, style.textArea)}
            input={input}
            meta={meta}
            {...others}
          />
        </div>
      );
    } else if (type === fieldType.bool) {
      return (
        <div className={style.field}>
          <div id={`error_${systemName}`} className={style.fieldName}>
            <Checkbox input={input} {...others} isDisabled={disable} />
            {getDisplayText(fieldInfo)}
            {required && <span className={style.required}>*</span>}
            {touched && error && <div className={style.required}>{error}</div>}
          </div>
        </div>
      );
    } else if (type === fieldType.picklist) {
      const defaultSelectOption = [{ text: ' --None-- ', value: '' }];
      let options = picklistValues.map(item => ({ text: item, value: item }));
      if (!required) {
        options = defaultSelectOption.concat(options);
      }
      return (
        <div className={style.field}>
          {prepend}
          <Select
            input={input}
            isDisabled={disable}
            options={options}
          />
        </div>
      );
    } else if (type === fieldType.date || type === fieldType.dateTime) {
      return (
        <div className={style.field}>
          {prepend}
          <DatePicker
            className={classNames(style.inputWidth, style.input)}
            dateFormatter={others.dateFormatter}
            isShowTime={type === fieldType.dateTime}
            input={input}
            meta={meta}
          />
        </div>
      );
    } else if (type === fieldType.multiPicklist) {
      return (
        <div className={style.field}>
          {prepend}
          <FormTransfer
            input={input}
            items={
              picklistValues ? picklistValues.map((item, index) => ({ id: index, name: item, value: item })) : []
            }
          />
        </div>
      );
    } else if (type === fieldType.reference) {
      return (
        <div className={style.field}>
          {prepend}
          <Lookup
            input={input}
            meta={meta}
            inputStyle={classNames(style.inputWidth, style.input)}
            isNeedSelector={isNeedSelector(fieldInfo)}
            selectOptions={fieldInfo.relatedObjects}
            onSalesforcelookUp={props.onSalesforcelookUp}
          />
        </div>
      );
    }
    return null;
  }
}

RenderField.propTypes = {
  fieldInfo: React.PropTypes.shape({
    label: React.PropTypes.string,
    required: React.PropTypes.bool,
    picklistValues: React.PropTypes.array,
    systemName: React.PropTypes.string,
    type: React.PropTypes.number,
    relatedObjects: React.PropTypes.array,
  }).isRequired,
};

export default RenderField;



