import React from 'react';
import classnames from 'classnames';
import TextArea from '../../TextArea/TextArea';
import Link from '../../Link/Link';
import { icons } from '../../../constants/enums';
import Icon from '../../Icon/Icon';
import Button from '../../Button/Button';
import style from './info.css';
import FeatureHelp from '../../FeatureHelp/FeatureHelp';

class AgentComment extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      agentComment: props.agentComment || '',
      errors: '',
    };

    this.handleValueChange = this.handleValueChange.bind(this);
    this.onSubmitComment = this.onSubmitComment.bind(this);
    this.onCommentEditIconClick = this.onCommentEditIconClick.bind(this);
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (this.props.agentComment !== nextProps.agentComment ||
      this.props.readOnly !== nextProps.readOnly ||
      this.props.isNoteEdit !== nextProps.isNoteEdit ||
      this.props.isNoteLoading !== nextProps.isNoteLoading ||
      this.props.newFeatureNotify !== nextProps.newFeatureNotify ||
      this.props.ifEnableWrapupChat !== nextProps.ifEnableWrapupChat ||
      this.state.agentComment !== nextState.agentComment ||
      this.state.errors !== nextState.errors) {
      return true;
    }
    return false;
  }
  onSubmitComment() {
    let e = '';
    if (this.state.agentComment) {
      this.props.handleSubmitComment({ comment: this.state.agentComment });
    } else {
      e = 'Required';
    }
    this.setState({
      errors: e,
    });
  }
  onCommentEditIconClick() {
    this.setState({
      agentComment: this.props.agentComment || '',
      errors: '',
    });
    this.props.onCommentEditIconClick();
  }
  handleValueChange(e) {
    this.setState({
      agentComment: e.target.value,
      errors: '',
    });
  }
  render() {
    const { ifEnableWrapupChat,
    agentComment,
    isNoteEdit,
    readOnly,
    newFeatureNotify,
    onFeatrueClick,
  } = this.props;
    if (!ifEnableWrapupChat && !readOnly) {
      return (
        <div>
          <div className={style.featrue}>
            <FeatureHelp
              ifShow={!isNoteEdit && !readOnly && (newFeatureNotify & 2) == 0}
              text="Add a note to this chat. It saves time when you review the chats later."
              featrueNumber={2}
              onGotitClick={() => {
                if (onFeatrueClick) onFeatrueClick(newFeatureNotify + 2);
              }}
            />
          </div>
          <div className={classnames(style.Container, style.editable, !isNoteEdit && style.notEdit)}>
            <div className={style.title}>Note to this Chat</div>
            <div style={{ display: isNoteEdit ? 'none' : 'block' }}>
              { agentComment ?
                <div tabIndex={-1} className={classnames(style.line, style.editIconWrapper)}>
                  {agentComment}
                  <Icon
                    type={icons.typing} color="#aaa" size={14}
                    className={classnames(style.editIcon, style.commentEditIcon)} onClick={this.onCommentEditIconClick}
                  />
                </div>
                :
                  <div className={style.line}>
                    <Link onClick={this.onCommentEditIconClick}>(Click here to add...)</Link>
                  </div> }
            </div>
            { isNoteEdit &&
              <div className={style.formWrapper}>
                <div>
                  <div className={style.line}>
                    <TextArea
                      name="comment"
                      className={style.commentTextarea}
                      rows="5"
                      maxLength={512}
                      type="text"
                      input={{
                        value: this.state.agentComment,
                        onChange: this.handleValueChange,
                      }}
                      meta={{ touched: true, invalid: this.state.errors ? true : false, error: this.state.errors || '' }}
                      isDisabled={this.props.isNoteLoading}
                    />
                  </div>
                  <div className={style.buttonWrapper}>
                    <Button
                      className={style.okButton}
                      onClick={this.onSubmitComment}
                      disabled={this.props.isNoteLoading || this.props.agentComment === this.state.agentComment}
                      loading={this.props.isNoteLoading}
                      iconPosition="right"
                      text="OK"
                      type="primary"
                      htmlType="button"
                    />
                    <Button
                      onClick={this.props.onCommentCancelClick}
                      text="Cancel"
                      htmlType="button"
                    />
                  </div>
                </div>
              </div>
            }
          </div>
        </div>); }
    return null;
  }
}

AgentComment.propTypes = {
  ifEnableWrapupChat: React.PropTypes.bool,
  isNoteLoading: React.PropTypes.bool,
  isNoteEdit: React.PropTypes.bool,
  agentComment: React.PropTypes.string,
  handleSubmitComment: React.PropTypes.func,
  onCommentEditIconClick: React.PropTypes.func,
  onCommentCancelClick: React.PropTypes.func,
  readOnly: React.PropTypes.bool,
  newFeatureNotify: React.PropTypes.number,
  onFeatrueClick: React.PropTypes.func,
};
AgentComment.defaultProps = {
  isNoteEdit: false,
};

export default AgentComment;



