import React from 'react';
import * as _ from 'lodash';
import classnames from 'classnames';
import style from './FormTransfer.css';
import { icons } from '../../constants/enums';
import Icon from '../Icon/Icon';

class FormTransfer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedIndex: -1,
      unselectedIndex: -1,
    };

    this.selectAll = this.selectAll.bind(this);
    this.unselectAll = this.unselectAll.bind(this);
    this.selectOptionClick = this.selectOptionClick.bind(this);
    this.unSelectOptionClick = this.unSelectOptionClick.bind(this);
    this.selectOne = this.selectOne.bind(this);
    this.unselectOne = this.unselectOne.bind(this);
  }

  selectOptionClick(index) {
    this.setState({
      selectedIndex: index,
    });
  }

  unSelectOptionClick(index) {
    this.setState({
      unselectedIndex: index,
    });
  }

  selectAll() {
    const result = this.props.items.map(item => item.value);
    this.props.input.onChange(result.join(';'));
    this.setState({
      unselectedIndex: -1,
      selectedIndex: -1,
    });
  }

  unselectAll() {
    this.props.input.onChange('');
    this.setState({
      unselectedIndex: -1,
      selectedIndex: -1,
    });
  }

  selectOne() {
    if (this.state.unselectedIndex < 0) {
      return;
    }
    const temp =this.props.input.value.split(';');
    const selectedIndex = this.state.unselectedIndex;
    const s = _.find(this.props.items, item => item.id === selectedIndex);
    if (_.indexOf(temp,s.value) ===-1) {
      temp.push(s.value)
      this.props.input.onChange(temp.join(';'));
    }
    this.setState({
      unselectedIndex: -1,
    });
  }

  unselectOne() {
    if (this.state.selectedIndex < 0) {
      return;
    }
    const temp =this.props.input.value.split(';');
    const selectedIndex = this.state.selectedIndex;
    const s = _.find(this.props.items, item => item.id === selectedIndex);
    const result = temp.filter(item => item !==s.value);
    if (result.length > 0) this.props.input.onChange(result.join(';'));
    else this.props.input.onChange('');
    this.setState({
      selectedIndex: -1,
    });
  }
  render() {
    const temp =this.props.input.value.split(';');
    return (
      <div className={classnames(style.container, this.props.className)}>
        <div className={style.wrapper}>
          <h4 className={style.header}>
            Available
          </h4>
          <div className={classnames(style.rightMargin,style.optionsWrapper)}>
            {
              this.props.items.filter(item => _.indexOf(temp,item.value) ===-1).map((option, index) =>
                  (<div
                    key={`option_${index}`}
                    className={classnames(style.option,
                      this.state.unselectedIndex === option.id && style.selected)}
                    onClick={() => this.unSelectOptionClick(option.id)}
                  >
                    {option.name}
                  </div>)
                )
            }
            </div>
          </div>
          <div className={style.operations}>
            <div className={style.operation}>
              <Icon
                type={icons.transferSelectAll}
                size={20}
                className={style.icons}
                onClick={this.selectAll}
              />
            </div>
            <div className={style.operation}>
              <Icon
                type={icons.transferSelect}
                size={20}
                className={style.icons}
                onClick={this.selectOne}
              />
            </div>
            <div className={style.operation}>
              <Icon
                type={icons.transferUnselect}
                size={20}
                className={style.icons}
                onClick={this.unselectOne}
              />
            </div>
            <div className={style.operation}>
              <Icon
                type={icons.transferUnselectAll}
                size={20}
                className={style.icons}
                onClick={this.unselectAll}
              />
            </div>
          </div>
          <div className={style.wrapper}>
            <h4 className={style.header}>
              Chosen
            </h4>
            <div className={classnames(style.leftMargin,style.optionsWrapper)}>
              {
                this.props.items.filter(item => _.indexOf(temp,item.value) !==-1).map((option, index) =>
                  (<div
                    key={`option_${index}`}
                    className={classnames(style.option,
                      this.state.selectedIndex === option.id && style.selected)}
                    onClick={() => this.selectOptionClick(option.id)}
                  >
                    {option.name}
                  </div>)
                )
              }
            </div>
          </div>
      </div>
    );
  }
}

export default FormTransfer;

FormTransfer.propTypes = {
  className: React.PropTypes.string,
  items: React.PropTypes.arrayOf(React.PropTypes.shape({
    id: React.PropTypes.number.isRequired,
    name: React.PropTypes.string.isRequired,
    value: React.PropTypes.string.isRequired,
  })).isRequired,
  input: React.PropTypes.shape({
    value: React.PropTypes.string,
    onChange: React.PropTypes.func,
  }).isRequired,
};





