import React from 'react';
import classnames from 'classnames';
import Link from '../../Link/Link';
import style from './info.css';

class InfoHistory extends React.Component {
  shouldComponentUpdate(nextProps) {
    if (this.props.chatTimes !== nextProps.chatTimes ||
    this.props.visitorTimes !== nextProps.visitorTimes) {
      return true;
    }
    return false;
  }
  render() {
    const { chatTimes, visitorTimes, onChatTimesClick } = this.props;
    const chatText = chatTimes + (chatTimes > 1 ? ' Chats' : ' Chat');
    const visitorText = visitorTimes + (visitorTimes > 1 ? ' Visits' : ' Visit');
    return (<div className={classnames(style.Container, style.notEdit)}>
      <div className={style.title}>History</div>
      <div className={style.line} tabIndex={-1}>
        { chatTimes > 0 ? <Link onClick={onChatTimesClick}>{chatText}</Link>
          : <span>{chatText}</span>}
        <span className={style.seperator}>|</span>
        <span className="contentMenu">{visitorText}</span>
      </div>
    </div>);
  }
}

InfoHistory.propTypes = {
  chatTimes: React.PropTypes.number,
  visitorTimes: React.PropTypes.number,
  onChatTimesClick: React.PropTypes.func,
};

InfoHistory.examples = {
  'Default Example': {
    description: 'Default Example',
    component:
      <InfoHistory chatTimes={2} visitorTimes={5} />,
  },
};

export default InfoHistory;



