import React from 'react';
import * as _ from 'lodash';
import RenderField from './RenderField';
import style from './Form.css';
import Button from '../Button/Button';
import * as fieldType from '../../constants/enums/fieldType';
import { defaultValidate, getInitValues } from '../../utils/helper';
import memoryStore from '../../services/memoryStore';
import AdvanceCategory from './AdvanceCategory';

function checkIfAdvanceCategory(ifEnableAdvancedCategoryMode = false, field) {
  if (!ifEnableAdvancedCategoryMode) return field;
  const keyMap = {};
  const groups = [];
  field.options.forEach((opt) => {
    let index = keyMap[opt.groupId];
    let temp = { displayText: opt.groupName, id: opt.groupId, options: [] };
    if (_.isUndefined(index)) {
      index = groups.length;
      keyMap[opt.groupId] = index;
    } else {
      temp = groups[index];
    }
    temp.options.push(opt);
    groups[index] = temp;
  });
  return {
    id: field.id,
    type: field.type,
    displayText: field.displayText,
    ifRequired: field.ifRequired,
    groups,
  };
}
class WrapupForm extends React.Component {
  constructor(props) {
    super(props);
    this.defaultValues = getInitValues(props.fields,
                         props.fieldsValue,
                         this.props.ifEnableAdvancedCategoryMode);
    const cacheInitValues = memoryStore.get(`Wrapup_initValues_${props.chatId}`);
    this.initValues = cacheInitValues || this.defaultValues;
    const cacheState = memoryStore.get(`Wrapup_FormState_${props.chatId}`);
    if (cacheState) {
      this.state = cacheState;
    } else {
      this.state = {
        values: this.initValues,
        errors: {},
      };
    }
    this.handleValueChange = this.handleValueChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.reset = this.reset.bind(this);
  }
  componentWillReceiveProps(nextProps) {
    if (this.props.chatId !== nextProps.chatId) {
      const cacheState = memoryStore.get(`Wrapup_FormState_${nextProps.chatId}`);
      const cacheInitValues = memoryStore.get(`Wrapup_initValues_${nextProps.chatId}`);
      this.initValues = cacheInitValues || this.defaultValues;
      if (cacheState) {
        this.setState(cacheState);
      } else {
        this.setState({
          values: this.initValues,
          errors: {},
        });
      }
    } else if (nextProps.ifSubmitSuccess && nextProps.ifSubmitSuccess !== this.props.ifSubmitSuccess) {
      this.initValues = this.state.values;
      memoryStore.set(`Wrapup_initValues_${nextProps.chatId}`, this.state.values);
    }
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (this.props.chatId !== nextProps.chatId
      || this.props.ifSubmitLoading !== nextProps.ifSubmitLoading
      || this.props.disable !== nextProps.disable
      || !_.isEqual(this.state.values, nextState.values)
      || !_.isEqual(this.state.errors, nextState.errors)) {
      return true;
    }
    return false;
  }
  componentDidUpdate(prevProps, prevState) {
    if (this.props.chatId === prevProps.chatId &&
        (!_.isEqual(this.state.values, prevState.values)
          || !_.isEqual(this.state.errors, prevState.errors))) {
      memoryStore.set(`Wrapup_FormState_${this.props.chatId}`, this.state);
    }
  }
  onSubmit() {
    const e = defaultValidate(this.state.values,
              this.props.fields,
              this.props.ifEnableAdvancedCategoryMode);
    if (Object.keys(e).length === 0) {
      this.props.onSubmit(this.state.values);
    } else {
      const name = Object.keys(e)[0];
      const errorElement = document.getElementById(`error_${name}`);
      if (errorElement) {
        if (errorElement.scrollIntoViewIfNeeded) errorElement.scrollIntoViewIfNeeded();
        else errorElement.scrollIntoView();
      }
    }
    this.setState({
      errors: e,
    });
  }
  reset() {
    this.props.onCancel();
    this.setState({
      values: this.initValues || {},
      errors: {},
    });
  }
  handleValueChange(name, value, fieldId = null) {
    const e = Object.assign({}, this.state.errors);
    const key = name || fieldId;
    if (e[key]) {
      e[key] = '';
    }
    const temp = Object.assign({}, this.state.values);
    temp[key] = value;
    if (!_.isEqual(temp, this.state.values)) {
      this.setState({
        values: temp,
        errors: e,
      });
    }
  }
  render() {
    const props = this.props;
    return (
      <div className={props.className}>
        <div>
          { props.fields.map((field, idx) => {
            const name = `${field.displayText}_${field.id}`;
            if (field.type === fieldType.TextArea || field.type === fieldType.WrapupComment
              || field.type === fieldType.Text) {
              return (<RenderField
                disable={props.disable} name={name} fieldInfo={field} key={idx}
                input={{ value: this.state.values[name] || '',
                  onChange: e => this.handleValueChange(name, e.target.value) }}
                meta={{ touched: true, invalid: this.state.errors[name] ? true : false, error: this.state.errors[name] || '' }}
              />);
            } else if (field.type === fieldType.CheckBox) {
              return (<RenderField
                disable={props.disable} name={name} fieldInfo={field} key={idx}
                input={{ value: this.state.values[name] || false,
                  onChange: value => this.handleValueChange(name, value) }}
                meta={{ touched: true, invalid: this.state.errors[name] ? true : false, error: this.state.errors[name] || '' }}
              />);
            } else if (field.type === fieldType.WrapupCategory) {
              const newField = checkIfAdvanceCategory(props.ifEnableAdvancedCategoryMode, field);
              if (!props.ifEnableAdvancedCategoryMode) {
                return (<RenderField
                  disable={props.disable} name={name} fieldInfo={newField} key={idx}
                  input={{ value: this.state.values[name] || '',
                    onChange: value => this.handleValueChange(name, value) }}
                  meta={{ touched: true, invalid: !!this.state.errors[name], error: this.state.errors[name] || '' }}
                />);
              }
              return (
                <AdvanceCategory
                  key={field.id}
                  chatId={props.chatId}
                  fieldInfo={newField}
                  disable={props.disable}
                  fieldId={field.id}
                  meta={{ touched: true, invalid: !!this.state.errors[name], error: this.state.errors[name] || '' }}
                  valueChange={val => this.handleValueChange(null, val, field.id)}
                />
              );
            } else {
              return (<RenderField
                disable={props.disable} name={name} fieldInfo={field} key={idx}
                input={{ value: this.state.values[name] || '',
                  onChange: value => this.handleValueChange(name, value) }}
                meta={{ touched: true, invalid: this.state.errors[name] ? true : false, error: this.state.errors[name] || '' }}
              />);
            }
          })
        }
        </div>
        <div className={style.buttonWrapper}>
          <Button
            onClick={this.onSubmit}
            className={style.marginRight20}
            disabled={props.disable || props.ifSubmitLoading || _.isEqual(this.initValues, this.state.values)}
            loading={props.ifSubmitLoading}
            iconPosition="right"
            text={props.loading ? 'Loading' : 'Save'}
            type="primary"
            htmlType="button"
          />
          <Button
            onClick={this.reset}
            text="Discard"
            disabled={props.disable || props.ifSubmitLoading || _.isEqual(this.initValues, this.state.values)}
            htmlType="button"
          />
        </div>
      </div>);
  }
}

WrapupForm.propTypes = {
  className: React.PropTypes.string,
  chatId: React.PropTypes.number,
  onSubmit: React.PropTypes.func,
  onCancel: React.PropTypes.func,
  fieldsValue: React.PropTypes.arrayOf(React.PropTypes.shape({
    name: React.PropTypes.string,
    value: React.PropTypes.oneOfType([React.PropTypes.string, React.PropTypes.bool]),
    id: React.PropTypes.number,
  })),
  fields: React.PropTypes.arrayOf(React.PropTypes.shape({
    displayText: React.PropTypes.string,
    ifRequired: React.PropTypes.bool,
    options: React.PropTypes.array,
    type: React.PropTypes.number,
    id: React.PropTypes.number,
  })).isRequired,
  ifSubmitLoading: React.PropTypes.bool,
  ifSubmitSuccess: React.PropTypes.bool,
  ifEnableAdvancedCategoryMode: React.PropTypes.bool,
  disable: React.PropTypes.bool,
};

export default WrapupForm;



