import React from 'react';
import ReactTooltip from 'react-tooltip';
import classnames from 'classnames';
import Link from '../../Link/Link';
import commonStyle from '../../CommonResources/common.css';
import style from './info.css';
import { shell } from '../../../services/electronAPI';

const getReferrer = (referrer, searchEngine, searchEngineKeywords, tooltipData) => {
  let result = '';
  if (searchEngine || searchEngineKeywords) {
    result = searchEngine;
    if (searchEngineKeywords) {
      result += ` (${searchEngineKeywords})`;
    }
    if (referrer) {
       return <Link  data-tip={tooltipData} data-for="wherefrom" className={style.wherefromLink} newWindow href={referrer}>{result}</Link>;
    }
  } else if (referrer) {
    result = <Link  data-tip={tooltipData} data-for="wherefrom" className={style.wherefromLink} newWindow href={referrer}>{referrer}</Link>;
  }
  return result;
};

const getReferrerTooltip = (referrer, searchEngine, searchEngineKeywords) => {
  let result = '';
  if (referrer) {
    result += `Referrer:<br>${referrer}<br>`;
  }
  if (searchEngine) {
    result += `Search Engine: ${searchEngine}<br>`;
  }
  if (searchEngineKeywords) {
    result += `Keywords: ${searchEngineKeywords}<br>`;
  }
  if (result.length > 0) {
    result = result.substring(0, result.length - 4);
  }
  return result;
};
class WhereFrom extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      x: 0,
      y: 0
    };
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (this.props.referrer !== nextProps.referrer ||
    this.props.searchEngine !== nextProps.searchEngine ||
    this.props.searchEngineKeywords !== nextProps.searchEngineKeywords ||
    this.state.x !== nextState.x ||
    this.state.y !== nextState.y) {
      return true;
    }
    return false;
  }
  render() {
    const { referrer, searchEngine, searchEngineKeywords } = this.props;
    const tooltipData = getReferrerTooltip(referrer, searchEngine, searchEngineKeywords);
    const data = getReferrer(referrer, searchEngine, searchEngineKeywords, tooltipData);
    if (data) {
      return (<div className={classnames(style.Container, style.notEdit)}>
        <div className={style.title}>Where From</div>
        <div id="wherefromDiv" className={classnames(style.wherefromDiv, style.line)} tabIndex={-1}>
          <div className={style.inlineBlock}
            onMouseDown={
              (e) => {
                if(e.nativeEvent.button == 2){
                    document.getElementById("wherefromDiv").focus();
                    let tempX = e.nativeEvent.x;
                    let tempY = e.nativeEvent.y;
                    if (window.innerWidth - e.nativeEvent.x < 150) {
                      tempX = window.innerWidth - 150;
                      tempY = e.nativeEvent.y + 10;
                    }
                    this.setState({x:tempX, y: tempY});

                }else {
                  this.setState({x:-9999, y: -9999});
                }
              }}
          >
          {data}
          <div className={style.wherefromMenuWrapper} style={{ left: this.state.x, top: this.state.y }}>
              <ul
                className={style.wherefromMenu}
              >
                <li onMouseDown={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    this.setState({x:-9999, y: -9999});
                    shell.openExternal(referrer);
                  }}>Open in browser</li>
                <li onMouseDown={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    var copyElement = document.getElementById('copyDiv');
                    copyElement.select();
                    document.execCommand('copy');
                  }}>Copy the link</li>
              </ul>
            </div>
          </div>
        </div>
         <textarea id="copyDiv" className={style.copyArea} defaultValue={referrer} />
        <ReactTooltip
          id="wherefrom"
          effect="solid"
          place="bottom"
          multiline
          class={classnames(style.tooltip, commonStyle.tooltipInfo)}
        />
      </div>); }
    return null;
  }
}

WhereFrom.propTypes = {
  referrer: React.PropTypes.string,
  searchEngine: React.PropTypes.string,
  searchEngineKeywords: React.PropTypes.string,
};

WhereFrom.examples = {
  'Default Example': {
    description: 'Default Example',
    component:
      <WhereFrom referrer="https://hosted.comm100.com/LiveChat/VisitorMonitor.aspx?siteId=194641" searchEngine="" searchEngineKeywords="" />,
  },
};

export default WhereFrom;



