import React from 'react';
import classnames from 'classnames';
import * as _ from 'lodash';
import style from './info.css';

class InfoVisitorSegment extends React.Component {
  shouldComponentUpdate(nextProps) {
    if (!_.isEqual(this.props.segments, nextProps.segments)) {
      return true;
    }
    return false;
  }
  render() {
    const { ifEnableSegmentation, segments } = this.props;
    // const SegmentVisible = segments.length > 0;
    if (ifEnableSegmentation) {
      return (<div className={classnames(style.Container, style.notEdit)}>
        <div className={style.title}>Visitor Segment</div>
        <div className={classnames(style.line, 'contentMenu')} tabIndex={-1}>
          {segments.map((segment, i) =>
            (<div className={style.segmentWrapper} key={`segmenti_${i}`}><i
              className={style.segments}
              style={{ backgroundColor: segment.color }}
            /><span>{segment.name}</span></div>))
          }
        </div>
      </div>);
    }
    return null;
  }
}

InfoVisitorSegment.propTypes = {
  ifEnableSegmentation: React.PropTypes.bool,
  segments: React.PropTypes.arrayOf(React.PropTypes.shape({})),
};
InfoVisitorSegment.defaultProps = {
  ifEnableSegmentation: false,
};
InfoVisitorSegment.examples = {
  'Default Example': {
    description: 'Default Example',
    component:
      <InfoVisitorSegment
        ifEnableSegmentation
        segments={[{ name: 'Chatted before', color: '#337ab7' }, { name: 'test', color: '#bbb' }]}
      />,
  },
};

export default InfoVisitorSegment;



