import { connect } from 'react-redux';
import * as perf from 'src/utils/perf';
import VisitorDetailsTab from '../components/Tabs/VisitorDetailsTab';
import * as visitorDetailsTab from './Business/visitorDetailsTab';
import * as visitorDetailsActions from '../actions/visitorDetails';

let mapStateToProps = state => ({
  selected: visitorDetailsTab.getSelectedTab(state),
  components: visitorDetailsTab.getTabs(state),
});

const mapDispatchToProps = dispatch => ({
  onTabSelect(tab) {
    dispatch(visitorDetailsActions.onDetailsTabSelect(tab));
  },
});

if (process.env.NODE_ENV !== 'production') {
  mapStateToProps = perf.markFunctionCall('VisitorDetailsTab: mapStateToProps', mapStateToProps);
}

export default connect(mapStateToProps, mapDispatchToProps)(VisitorDetailsTab);



