import React, { PropTypes, Component } from 'react';
import classnames from 'classnames';
import ReactTooltip from 'react-tooltip';
import { isNullOrEmpty } from '../../../utils/str';
import { formatTimeWithMillSecondKeep2Unit } from '../../../utils/time';
import Link from '../../Link/Link';
import style from './navigation.css';
import agentTabStyle from '../AgentTab.css';
import NavigationFirstItem from './NavigationFirstItem';
import Item from './NavigationItem';
import commonStyle from '../../CommonResources/common.css';
import { shouldComponentUpdateGen } from '../../../utils/helper';


const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'visitor navigation',
  ['visitorId', 'ifNavigationLoading', 'timezoneoffset']
);


class Navigation extends Component {

  componentDidMount() {
    if (this.props.navigationItems.length === 0) {
      this.props.refreshNavigation(this.props.visitorId);
    }
  }

  shouldComponentUpdate(nextProps) {
    return this.props.navigationItems.length !== nextProps.navigationItems.length ||
    shouldComponentUpdateHelper(this.props, nextProps);
  }


  componentDidUpdate(prvProps) {
    if (prvProps.visitorId !== this.props.visitorId
    && this.props.navigationItems.length === 0) {
      this.props.refreshNavigation(this.props.visitorId);
    }
    ReactTooltip.rebuild();
  }


  render() {
    return (
      <div className={style.navigation}>
        <div className={style.infoline}>
          {
            this.props.ifNavigationLoading ?
            (<div className={agentTabStyle.loading}>Loading...</div>) :
            (
              <div>
                <Link
                  onClick={() => this.props.refreshNavigation(
                            this.props.visitorId)}
                  className={agentTabStyle.refresh}
                >
                  Refresh
                </Link>
              </div>
            )
          }
        </div>

        {
          !this.props.ifNavigationLoading && (
            (this.props.navigationItems === undefined ||
            this.props.navigationItems.length === 0) ?
            (<div className={style.infoline}>
                Oops! This visitor has no navigation records yet.
            </div>) :
            (this.props.navigationItems.map((item, i, list) =>
              (
                (item.isCurrentPage) ?
              (<NavigationFirstItem
                key={`navigation_${item.id}`}
                title={isNullOrEmpty(item.title) ? item.pageURL : item.title}
                pageURL={item.pageURL}
                timezoneoffset={this.props.timezoneoffset}
                time={item.time}
              />)
                :
                (<Item
                  key={`navigation_${item.id}`}
                  title={isNullOrEmpty(item.title) ? item.pageURL : item.title}
                  pageURL={item.pageURL}
                  time={
                      formatTimeWithMillSecondKeep2Unit(list[i - 1].time - item.time)
                  }
                />)
              )
          )))
        }

        <ReactTooltip
          id="visitUrl"
          effect="solid"
          place="bottom"
          multiline
          class={style.tooltip}
        />
      </div>
    );
  }
}
Navigation.propTypes = {
  navigationItems: PropTypes.arrayOf(PropTypes.shape({
    isCurrentPage: PropTypes.bool,
    title: PropTypes.string.isRequired,
    pageURL: PropTypes.string.isRequired,
    time: PropTypes.instanceOf(Date).isRequired,
  }).isRequired,
   ).isRequired,

  visitorId: PropTypes.number,
  ifNavigationLoading: PropTypes.bool,
  timezoneoffset: PropTypes.number,

  refreshNavigation: PropTypes.func,
};

Navigation.examples = {
  'Default Example': {
    description: 'Navigation',
    component:
      <Navigation
        navigationItems={[
        { isCurrentPage: true, pageURL: 'comm100.com', time: '3h 20m' },
        { isCurrentPage: false, pageURL: 'comm100.com', time: '4h 20m' },
        { isCurrentPage: false, pageURL: 'comm100.com', time: '5h 20m' },
        { isCurrentPage: false, pageURL: 'comm100.com', time: '6h 20m' },
        ]}
      />,
  },
};

export default Navigation;



