import React, { PropTypes } from 'react';
import classnames from 'classnames';
import Link from '../../Link/Link';
import Icon from '../../Icon/Icon';
import { icons } from '../../../constants/enums';
import { isNullOrEmpty } from '../../../utils/str';
import * as senderType from '../../../constants/enums/senderType';
import style from './visitorHistory.css';


const HistoryChatDetals = props =>
   (
     <div>
       {props.refreshing && (<div className={style.detailLoading}>
         Loading...
       </div>) }
       {!props.refreshing &&
      (<div className={style.detail}>

        <span className={style.detailLink}>
          <Link
            onClick={() => {
              props.gotoVisitorChatDetail(props.chatInfo.detailUrl);
            }}
          >
          Details
          </Link>
        </span>


        {
          props.ifEnableWrapupChat && ((props.chatInfo.wrap_up !== undefined
          && props.chatInfo.wrap_up.length > 0)
          || !isNullOrEmpty(props.chatInfo.chatCategory.value)
          || !isNullOrEmpty(props.chatInfo.agentComment.value)) &&
          (<div>
            <div className={style.title}>Wrap-up Info:</div>
            <div className={classnames(style.information, 'contentMenu')}>
              {(!isNullOrEmpty(props.chatInfo.chatCategory.value)) &&
              (<div key={`visitorHistoryChat_${props.chatInfo.chatCategory.value}`}>
                {`${props.chatInfo.chatCategory.name}: ${props.chatInfo.chatCategory.value}`}
                </div>)
              }
              {(!isNullOrEmpty(props.chatInfo.agentComment.value)) &&
              (<div key={`visitorHistoryChat_${props.chatInfo.agentComment.value}`}>
                {`${props.chatInfo.agentComment.name}: ${props.chatInfo.agentComment.value}`}
                </div>)
              }
              {props.chatInfo.wrap_up && props.chatInfo.wrap_up.map(item =>
              (
                <div key={`visitorHistoryChat_${item.id}`}>{item.name}: {item.value}</div>
              ))}
            </div>
            <div className={style.splitLine} />
          </div>)}


        {(props.chatInfo.customVariables !== undefined &&
          props.chatInfo.customVariables.length > 0) &&
         (<div>
           <div className={style.title}>Custom Variable:</div>
           <div className={classnames(style.information, 'contentMenu')}>
             {
            props.chatInfo.customVariables.map(item =>
             (
               <div key={`visitorHistoryChat_${item.name}`}>
                 <span>{item.name}: </span>
                 {item.value && item.url &&
                   <Link newWindow href={item.url}>{item.value}</Link>
                 }
                 {item.value && !item.url &&
                   <span>{item.value}</span>
                 }
               </div>
             ))
           }
           </div>
           <div className={style.splitLine} />
           </div>)}

        {
          ((props.chatInfo.pre_chat !== undefined && props.chatInfo.pre_chat.length > 0) ||
           (props.chatInfo.pre_chat_ser !== undefined && props.chatInfo.pre_chat_ser.length > 0)) &&
          (<div>
            <div className={style.title}>Pre-chat:</div>
            <div className={classnames(style.information, 'contentMenu')}>
              {props.chatInfo.pre_chat_ser.map(item =>
              (
                <div key={`visitorHistoryChat_${item.id}`}>{item.name}: {item.value}</div>
              ))}
              {props.chatInfo.pre_chat.map(item =>
              (
                <div key={`visitorHistoryChat_${item.id}`}>{item.name}: {item.value}</div>
              ))}
            </div>
            <div className={style.splitLine} />
          </div>)}


        <div className={style.title}>Chat Content:</div>
        <div className={classnames(style.information, 'contentMenu')}>
          {
            props.chatInfo.chatContent.map(item => (<div key={`${item.info} ${item.id}`}>
              {!isNullOrEmpty(item.sendMessageTime) && (`[${item.sendMessageTime}] `)}
              {(item.sendertype === senderType.operator) &&
              (
              <span className={style.agentMessage}>
                {item.senderName}
                </span>)}
              {(item.sendertype === senderType.visitor) &&
              (<span className={style.visitorMessage}>
                {item.senderName}
                </span>)}
              {((item.sendertype === senderType.visitor) ||
                (item.sendertype === senderType.operator))
              && ': ' }
              {!isNullOrEmpty(item.infoPrefix) && (`${item.infoPrefix}`) }
              {!isNullOrEmpty(item.info) && `${item.info}`}
              {!isNullOrEmpty(item.endTime) && `(${item.endTime})`}
            </div>))
          }
        </div>
      </div>)}
     </div>)
;
HistoryChatDetals.propTypes = {
  refreshing: PropTypes.bool.isRequired,
  chatInfo: PropTypes.shape({
    id: PropTypes.number,
    detailUrl: PropTypes.string,
    customVariables: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    })).isRequired,

    chatContent: PropTypes.arrayOf(PropTypes.shape({
      id: PropTypes.number,
      senderName: PropTypes.string,
      sendertype: PropTypes.number,
      infoPrefix: PropTypes.string,
      sendMessageTime: PropTypes.string,
      endTime: PropTypes.string,
      info: PropTypes.string,
    })).isRequired,
    pre_chat_ser: PropTypes.arrayOf(PropTypes.shape({
      id: PropTypes.number.isRequired,
      name: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    }).isRequired),
    pre_chat: PropTypes.arrayOf(PropTypes.shape({
      id: PropTypes.number.isRequired,
      name: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    })).isRequired,
    wrap_up: PropTypes.arrayOf(PropTypes.shape({
      id: PropTypes.number.isRequired,
      name: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    })).isRequired,
    agentComment: PropTypes.shape({
      name: PropTypes.string.isRquired,
      value: PropTypes.string.isRquired,
    }),
    chatCategory: PropTypes.shape({
      name: PropTypes.string.isRquired,
      value: PropTypes.string.isRquired,
    }),
  }),
  gotoVisitorChatDetail: PropTypes.func.isRequired,
};
export default HistoryChatDetals;



