import React from 'react';

class HighLight extends React.Component {
  render() {
    const { text, patt } = this.props;
    let result;
    let i = 0;
    const companents = [];
    while ((result = patt.exec(text)) != null) {
      const temp = text.substring(i, result.index);
      companents.push(temp);
      companents.push((<span key={i} style={{ fontStyle: 'normal', background: '#ffff00' }}>{result[0]}</span>));
      i = patt.lastIndex;
    }
    if (i <= text.length) {
      companents.push(text.substring(i));
    }
    return (
      <div>
        {companents}
      </div>);
  }
}

HighLight.propTypes = {
  text: React.PropTypes.string,
  patt: React.PropTypes.instanceOf(RegExp),
};

export default HighLight;



