import { connect } from 'react-redux';
import * as _ from 'lodash';
import 'moment/src/locale/zh-cn';
import 'moment/src/locale/en-gb';
import * as selectors from '../reducers/selectors';
import Salesforce from '../components/Tabs/Salesforce/Salesforce';
import * as enumPage from '../constants/enums/page';
import * as SFPurposeInManualMode from '../constants/enums/SFPurposeInManualMode';
import * as salesforceActions from '../actions/salesforce';
import { contactInfo, LeadInfo } from '../constants/enums/salesforceInfoType';
import { format } from '../utils/time';
import { isNullOrEmpty } from '../utils/str';
import { getComm100fieldValues, getSFObjectId, getSFUTCTime,
  convertSFDateTimeToLocalDate, convertSFDateToLocalDate } from '../components/Tabs/Salesforce/util';
import * as salesforceConstants from '../constants/salesforce';

const fiedsFilter = (fields, filterfields, layoutfields, dateFormat) =>
((fields === undefined ||
  fields === null) ? undefined :
  fields.filter(item =>
     filterfields.some(element => element.sfFeildSystemName === item.name))
     .map((item) => {
       let label = item.name;
       let value = item.value;
       layoutfields.some((layout) => {
         const isExist = layout.systemName === item.name;
         if (isExist) {
           label = layout.label;
           if (layout.type === salesforceConstants.fieldType.dateTime &&
           !isNullOrEmpty(value)) {
             value = format(convertSFDateTimeToLocalDate(value), dateFormat);
           } else if (layout.type === salesforceConstants.fieldType.date &&
           !isNullOrEmpty(value)) {
             value = format(convertSFDateToLocalDate(value), dateFormat);
           } else if (layout.type === salesforceConstants.fieldType.reference) {
             value = item.displayValue;
             label = label.replace(' ID', '');
           }
         }
         return isExist;
       });
       return {
         name: item.name,
         label,
         value,
         displayValue: item.displayValue,
       };
     }));

const getLayoutfields = fields =>
   (fields === undefined ? [] :
   fields.filter(item => (item.visible)));

const createCaseSuccessInfo = 'A new case will be created when the chat ends.';
const attachCaseSuccessInfo = 'A new case will be created and attached to this contact when the chat ends.';
const attachTaskSuccessInfo = 'A new task will be created and attached to this lead when the chat ends.';

const getSalesforceStatus = (salesforceContactFields,
    salesforceLeadFields,
    salesforcePurposeInManualMode) => {
  let matchToContact = false;
  let matchToLead = false;
  let caseCreated = false;
  let taskCreated = false;
  let successInfo = '';

  if (salesforceContactFields !== undefined && salesforceContactFields.length > 0) {
    matchToContact = true;
    matchToLead = false;
  } else {
    matchToContact = false;
    if (salesforceLeadFields !== undefined && salesforceLeadFields.length > 0) {
      matchToLead = true;
    } else {
      matchToLead = false;
    }
  }

  switch (salesforcePurposeInManualMode) {
    case SFPurposeInManualMode.SFSDoNoting:
      taskCreated = false;
      caseCreated = false;
      successInfo = '';
      break;
    case SFPurposeInManualMode.SFSCreateCase:
      taskCreated = false;
      caseCreated = true;
      successInfo = createCaseSuccessInfo;
      break;
    case SFPurposeInManualMode.SFAttachCase:
      taskCreated = false;
      caseCreated = true;
      successInfo = attachCaseSuccessInfo;
      break;
    case SFPurposeInManualMode.SFSAttachTask:
      taskCreated = true;
      caseCreated = false;
      successInfo = attachTaskSuccessInfo;
      break;
    default:
      taskCreated = false;
      caseCreated = false;
      successInfo = '';
      break;
  }

  return {
    matchToContact,
    matchToLead,
    caseCreated,
    taskCreated,
    successInfo,
  };
};

const createSalesforceProps = (state) => {
  // const timezoneoffset = selectors.getTimeDelay(state);
  const datefmt = state.config.preference.dateTimeFormat;
  const page = selectors.getPage(state);
  let visitor;
  let chatId;
  if (page === enumPage.chats) {
    const chat = selectors.getSelectedChat(state) ?
      selectors.getSelectedChat(state) : undefined;
    if (chat) {
      chatId = chat.get('id');
      visitor = selectors.getVisitorByChatId(state, chatId);
    }
  } else if (page === enumPage.visitors) {
    visitor = selectors.getVisitor(state, selectors.getSelectedVisitorId(state));
  }
  if (!visitor) return {};
  const showForm = state.ui.mychats.salesforce.showForm;
  const ifShowLoading = !state.ui.mychats.salesforce.getSFIntegrationConfig;
  const editType = state.ui.mychats.salesforce.editType;
  const contactFields = state.config.salesforce.contactFields;
  const leadFields = state.config.salesforce.leadFields;

  const layoutContactFields = state.config.salesforce.contactFields === undefined ? [] :
  state.config.salesforce.contactFields;

  const layoutLeadFields = state.config.salesforce.leadFields === undefined ? [] :
  state.config.salesforce.leadFields;

  const layoutAccountFields = state.config.salesforce.accountFields === undefined ? [] :
  state.config.salesforce.accountFields;
  const leadId = getSFObjectId(visitor.get('salesforceLeadFields'));
  const contactId = getSFObjectId(visitor.get('salesforceContactFields'));

  let salesforceAccountFields = [];
  let salesforceLeadFields = [];
  let salesforceContactFields = [];
  if (state.config.salesforce.integrationConfig !== undefined) {
    const filterAccountFields = getLayoutfields(
      state.config.salesforce.integrationConfig.accountFields);
    const filterContactFields = getLayoutfields(
      state.config.salesforce.integrationConfig.contactFields);
    const filterLeadFields = getLayoutfields(
      state.config.salesforce.integrationConfig.leadFields);
    salesforceContactFields = fiedsFilter(visitor.get('salesforceContactFields'),
      filterContactFields, layoutContactFields, datefmt);
    salesforceLeadFields = fiedsFilter(visitor.get('salesforceLeadFields'),
      filterLeadFields, layoutLeadFields, datefmt);
    salesforceAccountFields = fiedsFilter(visitor.get('salesforceAccountFields'),
      filterAccountFields, layoutAccountFields, datefmt);
  }
  let departmentName = '';
  if (visitor.get('departmentId') > 0) {
    departmentName = selectors.getDepartmentNameById(state, visitor.get('departmentId'));
  }
  const integrationConfig = state.config.salesforce.integrationConfig;
  let contactComm100fieldValues = [];
  let leadComm100fieldValues = [];
  if (integrationConfig) {
    contactComm100fieldValues = getComm100fieldValues(
      integrationConfig.contactFields, visitor, departmentName);
    leadComm100fieldValues = getComm100fieldValues(
      integrationConfig.leadFields, visitor, departmentName);
  }

  let fields = [];
  let fieldsValues = [];
  let sfContactFormValue = [];
  let sfLeadFormValue = [];
  let contactShowFields;
  let leadShowFields;
  if (contactFields && contactFields.length > 0) {
    contactShowFields = contactFields
      .filter(field => field.layoutId >= 0 && field.createable)
      .sort((a, b) => a.layoutId - b.layoutId);
    sfContactFormValue = contactShowFields.map((field) => {
      const temp = _.find(visitor.get('salesforceContactFields'),
          contact => contact.name === field.systemName);
      if (temp) {
        return { name: temp.name, value: temp.value, displayValue: temp.displayValue };
      }
      const v = _.find(contactComm100fieldValues, contact => contact.name === field.systemName);
      if (v) {
        return { name: field.systemName, value: v.value, displayValue: v.displayValue };
      }
      return null;
    }).filter(field => field !== null);
  }
  if (leadFields && leadFields.length > 0) {
    leadShowFields = leadFields.filter(field => field.layoutId >= 0 && field.createable)
      .sort((a, b) => a.layoutId - b.layoutId);
    sfLeadFormValue = leadShowFields.map((field) => {
      const temp = _.find(visitor.get('salesforceLeadFields'),
        contact => contact.name === field.systemName);
      if (temp) {
        return { name: temp.name, value: temp.value, displayValue: temp.displayValue };
      }
      const v = _.find(leadComm100fieldValues, lead => lead.name === field.systemName);
      if (v) {
        return { name: field.systemName, value: v.value, displayValue: v.displayValue };
      }
      return null;
    }).filter(field => field !== null);
  }
  if (editType === contactInfo) {
    fields = contactShowFields;
    fieldsValues = sfContactFormValue;
  } else if (editType === LeadInfo) {
    fields = leadShowFields;
    fieldsValues = sfLeadFormValue;
  }
  const statusInfo = getSalesforceStatus(salesforceContactFields,
    salesforceLeadFields,
    visitor.get('salesforcePurposeInManualMode'));
  const chatExists = selectors.ifChatExists(state, chatId);
  return {
    getSFObjectFieldsError: state.ui.mychats.getSFObjectFieldsError ? 'Server Exception: Get Salesforce Object Fields Error' : '',
    statusInfo,
    chatId,
    visitorId: visitor.get('id'),
    leadId,
    contactId,
    salesforceContactFields: salesforceContactFields || [],
    salesforceLeadFields: salesforceLeadFields || [],
    salesforceAccountFields: salesforceAccountFields || [],
    fields: fields || [],
    fieldsValues: fieldsValues || [],
    dateFormatter: 'YYYY-MM-DD',
    dateTimeFormatter: 'YYYY-MM-DD HH:mm',
    showForm,
    ifShowLoading,
    editType,
    ifSalesforceLoading: state.ui.mychats.salesforce.ifSalesforceLoading,
    salesforceSubmitError: state.ui.mychats.salesforce.salesforceSubmitError,
    waiting: state.ui.mychats.salesforce.waiting,
    chatExists,
    errorMessage: state.ui.mychats.salesforce.errorMessage,
  };
};

const mergeProps = (stateProps, dispatchProps) => {
  const { dispatch } = dispatchProps;

  const dispatchObject = {
    onSubmitSalesforceForm(form) {
      dispatch(salesforceActions.handleSubmitSalesforce());
      const type = stateProps.editType;
      let id = '';
      if (type === contactInfo) {
        id = stateProps.contactId;
      } else if (type === LeadInfo) {
        id = stateProps.leadId;
      }
      const result = stateProps.fields.map((field) => {
        let formValue = '';
        if (field.type === salesforceConstants.fieldType.reference) {
          const temp = form[field.systemName];
          formValue = temp ? temp.id : '';
        } else if (field.type === salesforceConstants.fieldType.date || field.type === salesforceConstants.fieldType.dateTime) {
          const tempTime = form[field.systemName];
          if (tempTime) {
            if (typeof tempTime === 'string') {
              formValue = tempTime;
            } else {
              formValue = getSFUTCTime(tempTime.toDate());
            }
          } else {
            formValue = '';
          }
        } else if (field.type === salesforceConstants.fieldType.bool) {
          formValue = !!form[field.systemName];
        } else {
          formValue = form[field.systemName] || '';
        }
        if (field.required && field.type !== salesforceConstants.fieldType.bool && !formValue) {
          formValue = '-';
        }
        return { name: field.systemName, value: formValue, displayValue: '' };
      });
      if (id) {
        result.push({ name: 'Id', value: id, displayValue: '' });
        if (type === contactInfo) {
          dispatch(salesforceActions.agentEditSFContact(stateProps.visitorId, result));
        } else if (type === LeadInfo) {
          dispatch(salesforceActions.agentEditSFLead(stateProps.visitorId, result));
        }
      } else {
        if (type === contactInfo) {
          dispatch(salesforceActions.agentCreateSFContact(stateProps.visitorId, result));
        } else if (type === LeadInfo) {
          dispatch(salesforceActions.agentCreateSFLead(stateProps.visitorId, result));
        }
      }
    },
    onCancelClick() {
      dispatch(salesforceActions.onCancelClick());
    },
    onEditOrCreate(type) {
      dispatch(salesforceActions.onEditOrCreate(type));
    },
    onAttachToLead(visitorId, leadId) {
      dispatch(salesforceActions.handleAttach());
      dispatch(salesforceActions.agentAttachSFTaskToLead(visitorId, leadId));
    },
    onAttachToContact(visitorId, contactId) {
      dispatch(salesforceActions.handleAttach());
      dispatch(salesforceActions.agentAttachSFCaseToContact(visitorId, contactId));
    },
    onCreateCase(visitorId) {
      dispatch(salesforceActions.handleAttach());
      dispatch(salesforceActions.agentCreateSFCase(visitorId));
    },
    onSalesforcelookUp(type, keyword, next) {
      dispatch(salesforceActions.agentLookupSFObject(type, keyword, next));
    },
  };
  return {
    ...stateProps,
    ...dispatchObject,
  };
};


export default connect(createSalesforceProps, null, mergeProps)(Salesforce);



