import axios from 'axios';
import { shell } from '../../services/electronAPI';
import { agentTabs, chatStatus } from '../../constants/enums';
import * as selectors from '../../reducers/selectors';
import { encrypt } from '../../utils/simpleEncrypt';

export const getTrialInfo = (state) => {
  const trialInfo = selectors.getTrialInfo(state);
  if (!trialInfo) return '';

  if (trialInfo.trialPeriodReminderDays > 0 && !trialInfo.haveBillingInfo) {
    return `Free trial ${trialInfo.trialPeriodReminderDays} days left`;
  } else if (trialInfo.trialPeriodReminderDays === 0) {
    return 'You free trial will expire today!';
  }
  return '';
};

export const getAgentUrls = (state, isWeb = false) => {
  const siteId = state.agent.siteId;
  const email = state.agent.email;
  const password = escape(encrypt(state.agent.password));
  const purchaseUrl = `${selectors.getBillingURL(state)}/BuyNowRedirect.aspx?siteId=${siteId}`;
  const dashBoardUrl = `${selectors.getLiveChatDashboardUrl(state)}?siteId=${siteId}`;
  const addAgentsUrl = `${selectors.getAdminAddAgentsUrl(state)}?siteId=${siteId}`;
  const loginUrl = selectors.getLoginUrl(state);
  if (isWeb) return [isWeb, dashBoardUrl, purchaseUrl];
  const autoLoginUrl =
    `${selectors.getAutoLoginManagerUrl(state)}?siteId=${siteId}&email=${email}&password=${password}`;
  return { isWeb,
    dashBoardUrl,
    purchaseUrl,
    // attachTicketUrl,
    loginUrl,
    autoLoginUrl,
    addAgentsUrl };
};

export const getAttachTicketURL = (state, ticketId, isWeb = false) => {
  const siteId = state.agent.siteId;
  const email = state.agent.email;
  const password = escape(encrypt(state.agent.password));
  const attachTicketUrl = `${selectors.getAttachTicketURL(state)}?siteId=${siteId}#ticket${ticketId}`;
  const loginUrl = selectors.getLoginUrl(state);
  if (isWeb) return [isWeb];
  const autoLoginUrl =
    `${selectors.getAutoLoginManagerUrl(state)}?siteId=${siteId}&email=${email}&password=${password}`;
  return {
    isWeb,
    attachTicketUrl,
    loginUrl,
    autoLoginUrl,
  };
};

export function gotoSpecificUrl(isWeb, url, loginUrl, autoLoginUrl) {
  const currentWindow = process.env.IS_WEB_APP ? window.open('') : null;
  if (isWeb) {
    currentWindow.location = url;
    return;
  }
  axios.get(autoLoginUrl)
  .then((rsp) => {
    const skipUrl = `${loginUrl}?sessionId=${rsp.data}&retUrl=${escape(url)}`;
    if (process.env.IS_WEB_APP) {
      currentWindow.location = skipUrl;
    } else {
      shell.openExternal(skipUrl);
    }
  });
}

export const gotoVisitorChatDetail = ({ purchaseUrl, loginUrl, autoLoginUrl }) => {
  gotoSpecificUrl(false, purchaseUrl, loginUrl, autoLoginUrl);
};

export const gotoPurchase = ({ isWeb, purchaseUrl, loginUrl, autoLoginUrl }) => {
  gotoSpecificUrl(isWeb, purchaseUrl, loginUrl, autoLoginUrl);
};

export const gotoBuyNow = ({ isWeb, buyNowUrl, loginUrl, autoLoginUrl }) => {
  gotoSpecificUrl(isWeb, buyNowUrl, loginUrl, autoLoginUrl);
};

export const gotoDashboad = ({ isWeb, dashBoardUrl, loginUrl, autoLoginUrl }) => {
  gotoSpecificUrl(isWeb, dashBoardUrl, loginUrl, autoLoginUrl);
};

export const gotoAgentsList = ({ isWeb, addAgentsUrl, loginUrl, autoLoginUrl }) => {
  gotoSpecificUrl(isWeb, addAgentsUrl, loginUrl, autoLoginUrl);
};

export const gotoAttachTicket = ({ isWeb, attachTicketUrl, loginUrl, autoLoginUrl }) => {
  gotoSpecificUrl(isWeb, attachTicketUrl, loginUrl, autoLoginUrl);
};


export const getChatNumber = (state) => {
  if (selectors.getCurrentTab(state) === agentTabs.chat) return 0;
  return selectors.getChats(state).reduce((count, val) => {
    if (selectors.getIsUnfinishedWrapup(state, val.get('id')) &&
        !selectors.getWrapupSubmitted(state, val.get('id'))) {
      return count + 1;
    }
    const status = selectors.getVisitorByChatId(state, val.get('id')).get('status');
    if (status === chatStatus.waitingForChat ||
        status === chatStatus.transferring) {
      return count + 1;
    }
    return count + val.get('unreadMessages');
  }, 0);
};

export const getAgentNumber = (state) => {
  if (selectors.getCurrentTab(state) === agentTabs.agent) return 0;
  return selectors.getAgentChats(state).reduce((sum, val) => sum + val.get('unreadMessages'), 0);
};
export const checkAgentChatFileSixe = (state, file) =>
     selectors.getAgentChatFileSize(state) * 1024 * 1024 < file.size;

export const getSoundUrl = state => selectors.getSoundUrl(state);



