import React, { PropTypes } from 'react';
import Button from '../../Button/Button';
import style from './salesforce.css';
import { icons as enums } from '../../../constants/enums';
import Icon from '../../Icon/Icon';
import { contactInfo, LeadInfo } from '../../../constants/enums/salesforceInfoType';

const SalesforceBuggonGroup = props => (
  <div className={style.buttonGroup}>

    {(!(props.statusInfo.matchToContact
      || props.statusInfo.matchToLead
      || props.statusInfo.taskCreated
      || props.statusInfo.caseCreated)) &&
      (<div className={style.infoline}>
         This visitor is not available in your Salesforce.
      </div>)}

    {(props.statusInfo.taskCreated
            || props.statusInfo.caseCreated) &&
    (<div className={style.infoline2}>
      <span>{props.statusInfo.successInfo}</span>
    </div>)}

    {(!(props.statusInfo.matchToContact
          || props.statusInfo.matchToLead
          || props.statusInfo.taskCreated
            || props.statusInfo.caseCreated)) &&
    (<div className={style.buttonContaner}>
      <Button
        disabled={props.disabled}
        text="Create Lead & Attach Task" type="primary" className={style.button}
        onClick={() => { props.onCreateLead(LeadInfo); }}
      />
    </div>)}

    {(!(props.statusInfo.matchToContact
          || props.statusInfo.matchToLead
          || props.statusInfo.taskCreated
          || props.statusInfo.caseCreated)) &&
    (<div className={style.buttonContaner}>
      <Button
        disabled={props.disabled}
        text="Create Case" type="primary" className={style.button}
        onClick={() => { props.onCreateCase(props.visitorId); }}
      />
    </div>)}

    {(!(props.statusInfo.matchToContact
          || props.statusInfo.matchToLead
          || props.statusInfo.taskCreated
          || props.statusInfo.caseCreated)) &&
    (<div className={style.buttonContaner}>
      <Button
        disabled={props.disabled}
        text="Create Contact & Attach Case" type="primary" className={style.button}
        onClick={() => { props.onCreateContact(contactInfo); }}
      />
    </div>)}

    {(!(!props.statusInfo.matchToLead
          || props.statusInfo.taskCreated)) &&
    (<div className={style.buttonContaner}>
      <Button
        disabled={props.disabled}
        text="Attach Task to This Lead" type="primary" className={style.button}
        onClick={() => { props.onAttachToLead(props.visitorId, props.leadId); }}
      />
    </div>)}

    {(!(!props.statusInfo.matchToContact
          || props.statusInfo.caseCreated)) &&
    (<div className={style.buttonContaner}>
      <Button
        disabled={props.disabled}
        text="Attach Case to This Contact" type="primary" className={style.button}
        onClick={() => { props.onAttachToContact(props.visitorId, props.contactId); }}
      />
    </div>)}

  </div>);

SalesforceBuggonGroup.propTypes = {
  statusInfo: PropTypes.shape({
    matchToContact: PropTypes.bool.isRequired,
    matchToLead: PropTypes.bool.isRequired,
    caseCreated: PropTypes.bool.isRequired,
    taskCreated: PropTypes.bool.isRequired,
    successInfo: PropTypes.string.isRequired,
  }),
  visitorId: PropTypes.number,
  leadId: PropTypes.string,
  contactId: PropTypes.string,
  disabled: PropTypes.bool,
  onCreateContact: PropTypes.func,
  onCreateLead: PropTypes.func,
  onCreateCase: PropTypes.func,
  onAttachToLead: PropTypes.func,
  onAttachToContact: PropTypes.func,
};

export default SalesforceBuggonGroup;



