import React from 'react';
import ReactTooltip from 'react-tooltip';
import * as _ from 'lodash';
import classnames from 'classnames';
import { visitorDetailsTabs, icons } from '../../constants/enums';
import Icon from '../Icon/Icon';
import style from './VisitorDetailsTab.css';

/**
 * Configuration of ordered tabs
 */
const order = [
  {
    key: visitorDetailsTabs.info,
    text: 'Info',
    icon: icons.info,
    color: '#329FD9',
    tooltip: 'Visitor Info',
  },
  {
    key: visitorDetailsTabs.salesforce,
    text: 'Salesforce',
    icon: icons.salesforce,
    color: '#0b9dd6',
    tooltip: 'Salesforce',
  },
  {
    key: visitorDetailsTabs.wrapup,
    text: 'Wrap-up',
    icon: icons.wrapup,
    color: '#7ac2b7',
    tooltip: 'Wrap-up',
  },
  {
    key: visitorDetailsTabs.cannedMessage,
    text: 'Canned',
    icon: icons.canned,
    color: '#fcb603',
    tooltip: 'Canned',
  },
  {
    key: visitorDetailsTabs.nav,
    text: 'Navigation',
    icon: icons.navigation,
    color: '#7ebd3a',
    tooltip: 'Navigation',
  },
  {
    key: visitorDetailsTabs.history,
    text: 'History',
    icon: icons.history,
    color: '#f05030',
    tooltip: 'History',
  },

];

const clearSelect = () => {
  const element = document.querySelector('div[data-vmselect="open"]');
  if (element) {
    element.click();
  }
};
const clearSelectDebounce = _.debounce(clearSelect, 10);
/**
 * Create Unselected Icon for tab
 * @param {Object} config - One of element in `order` array
 * @param {Function} onTabSelect - Callback when this tab is selected
 * @returns {Object} React Component
 */
const getUnselectedIcon = (config, onTabSelect) => (
  <div
    className={classnames(style.unselected, style.icon)}
    data-tip={config.tooltip} data-for="visitordetails"
    onClick={() => {
      ReactTooltip.hide(document.getElementById(config.key));
      onTabSelect(config.key);
    }}
    key={config.key}
    id={config.key}
  >
    <Icon type={config.icon} size="normal" color="#888" hoverColor={config.color} />
  </div>
);

/**
 * Create Selected Icon for tab
 * @param {Object} config - One of element in `order` array
 * @returns {Object} React Component
 */
const getSelectedIcon = config => (
  <div className={style.selected} key={config.key}>
    <Icon type={config.icon} size="normal" color={config.color} />
    <span>{config.text}</span>
  </div>
);

/**
 * Get selected tab.
 *
 * It's possible that new provided props missing currently selected tab.
 * In this case, use the first tab as selected one.
 *
 * @param {string} selected - Previous selected
 * @param {object} components - Components configuration
 * @returns {string} selected tab
 */
const getSelected = (selected, components) => {
  if (components[selected]) return selected;
  return Object.keys(components)[0];
};

class VisitorDetailsTab extends React.Component {

  componentDidUpdate(prevProps) {
    if (prevProps.selected !== this.props.selected) {
      ReactTooltip.rebuild();
    }
  }
  render() {
    const {
      components,
      selected,
      onTabSelect,
    } = this.props;
    const selectedTab = getSelected(selected, components);
    return (
      <div className={style.container}>
        <div className={style.header}>
          {
            order
              .filter(config => components[config.key])
              .map((config) => {
                if (config.key === selectedTab) return getSelectedIcon(config);
                return getUnselectedIcon(config, onTabSelect);
              })
          }
        </div>
        <div
          className={style.content}
          onScroll={() => {
            clearSelectDebounce();
          }}
        >
          {
          order
            .filter(config => config.key === selectedTab)
            .map(config => components[config.key])
            .map(config => React.createElement(config.component, config.props))[0]
          }
        </div>
        <ReactTooltip
          id="visitordetails"
          effect="solid"
          place="bottom"
          class={style.tooltip}
        />
      </div>
    );
  }
}

VisitorDetailsTab.propTypes = {
  /**
   * configuration of displayed component when corresponding tab is selected.
   * Object key corresponds to tab type, should be `enums.visitorDetailsTabs`;
   * @property component - React Component to be displayed under tab
   * @property props - `props` used for this displayed component
   */
  components: React.PropTypes.objectOf(React.PropTypes.shape({
    component: React.PropTypes.func.isRequired,
    props: React.PropTypes.shape({}),
  })).isRequired,
  /** selected tab */
  selected: React.PropTypes.oneOf(order.map(element => element.key)).isRequired,
  /** callback function, triggered when a tab is selected */
  onTabSelect: React.PropTypes.func,
};

VisitorDetailsTab.examples = {
  'Default Example': {
    description: [
      'Use `selected` to determine which tab has been selected; ',
      'Use `components` to determine which tabs should be displayed.',
    ].join(''),
    component:
      <div style={{ width: '265px' }}>
        <VisitorDetailsTab
          selected={visitorDetailsTabs.info}
          onTabSelect={(tab) => { console.log(`Tab (${tab}) is selected`); }}
          components={{
            [visitorDetailsTabs.info]: {
              component: () => <div>Info Tab</div>,
              props: {},
            },
            [visitorDetailsTabs.salesforce]: {
              component: () => <div>Salesforce Tab</div>,
              props: {},
            },
            [visitorDetailsTabs.history]: {
              component: () => <div>History Tab</div>,
              props: {},
            },
            [visitorDetailsTabs.nav]: {
              component: () => <div>Nav Tab</div>,
              props: {},
            },
            [visitorDetailsTabs.cannedMessage]: {
              component: () => <div>Canned Message</div>,
              props: {},
            },
          }}
        />
      </div>,
  },
};

export default VisitorDetailsTab;



