import React from 'react';
import * as _ from 'lodash';
import classnames from 'classnames';
import Animate from 'rc-animate';
import Tooltip from 'src/components/Tooltip/Tooltip';
import Checkbox from '../Checkbox/Checkbox';
import Icon from '../Icon/Icon';
import { icons } from '../../constants/enums';
import style from './Tree.css';

/**
 * TreeNode
 */
class TreeNode extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dataLoading: false,
    };
    this.isdbClick = false;
    this.checkClick = false;
    this.onCheck = this.onCheck.bind(this);
    this.onSelect = this.onSelect.bind(this);
    this.onExpand = this.onExpand.bind(this);
    this.onDoubleClick = this.onDoubleClick.bind(this);
    this.timer = null;
  }
  componentDidMount() {
    if (!this.props.root._treeNodeInstances) {
      this.props.root._treeNodeInstances = [];
    }
    this.props.root._treeNodeInstances.push(this);
    Tooltip.rebuild();
  }
  componentWillUnmount() {
    if (this.timer !== null) {
      clearTimeout(this.timer);
      this.timer = null;
    }
  }
  onCheck() {
    this.checkClick = true;
    this.props.root.onCheck(this);
  }
  onSelect() {
    this.props.root.onSelect(this.props.id, this.props.value || this.props.title);
  }
  onDoubleClick() {
    this.props.root.onDoubleClick(this.props.id, this.props.value || this.props.title);
  }
  onExpand() {
    const _self = this;

    const callbackPromise = this.props.root.onExpand(this);
    if (callbackPromise && (typeof callbackPromise === 'undefined' ? 'undefined' : typeof callbackPromise) === 'object') {
      (function () {
        const setLoading = function setLoading(dataLoading) {
          _self.setState({ dataLoading });
        };
        setLoading(true);
        callbackPromise.then(() => {
          setLoading(false);
        }, () => {
          setLoading(false);
        });
      }());
    }
  }
  // keyboard event support
  onKeyDown(e) {
    e.preventDefault();
  }
  renderChildren(props) {
    const renderFirst = this.renderFirst;
    this.renderFirst = 1;
    let transitionAppear = true;
    if (!renderFirst && props.expanded) {
      transitionAppear = false;
    }
    const children = props.children;
    let newChildren = children;
    if (children && children.length > 0) {
      const animProps = {};
      if (props.openTransitionName) {
        animProps.transitionName = props.openTransitionName;
      } else if (typeof props.openAnimation === 'object') {
        animProps.animation = _.assign({}, props.openAnimation);
        if (!transitionAppear) {
          delete animProps.animation.appear;
        }
      }
      newChildren = (
        <Animate
          {...animProps}
          showProp="data-expanded"
          transitionAppear={transitionAppear}
          component=""
        >
          {!props.expanded ? null : <ul
            className={classnames(style['vm-tree-child-tree'],
            props.expanded && style['vm-tree-child-tree-open'])}
            data-expanded={props.expanded}
          >
            {React.Children.map(children, (item, index) =>
               props.root.renderTreeNode(item, index, props.pos)
            , props.root)}
          </ul>}
        </Animate>
      );
    }
    return newChildren;
  }
  render() {
    const props = this.props;
    const tip = {};
    const content = props.title;
    let canRenderSwitcher = true;
    let newChildren = this.renderChildren(props);
    if (!newChildren || newChildren === props.children) {
      // content = newChildren;
      newChildren = null;
      if (!newChildren || props.isLeaf) {
        canRenderSwitcher = false;
      }
    }
    if (props.tooltip) {
      tip['data-tip'] = props.value;
      tip['data-for'] = 'vmtree';
    }
    // const title = <span className={style['vm-tree-title']}>{content}</span>;
    const paddingLeft = props.paddingLeft + 17 * (props.pos.split('-').length - 2) + (canRenderSwitcher ? 0 : 17);
    return (
      <li className={classnames(props.className, !canRenderSwitcher && style['vm-tree-leaf'], props.disabled && style['vm-tree-treenode-disabled'])}>
        <div
          className={style['vm-tree-itemWrapper']}
          style={{ paddingLeft }}
          tabIndex={-1}
          onClick={(e) => {
            e.nativeEvent.preventDefault();
            e.nativeEvent.stopPropagation();
            if (this.checkClick) {
              this.checkClick = false;
              return;
            }
            if (props.isLeaf) {
              this.isdbClick = false;
              if (this.timer !== null) {
                clearTimeout(this.timer);
              }
              this.timer = window.setTimeout(() => {
                if (this.isdbClick) return;
                this.onSelect();
              }, 300);
            } else if (canRenderSwitcher) {
              this.onExpand();
            }
          }}
          onDoubleClick={(e) => {
            e.nativeEvent.preventDefault();
            e.nativeEvent.stopPropagation();
            if (!props.disabled) {
              if (props.isLeaf) {
                this.isdbClick = true;
                this.onDoubleClick();
              } else if (canRenderSwitcher) {
                this.onExpand();
              }
            }
          }}
        >
          {canRenderSwitcher &&
            <span className={style['vm-tree-switcher']} onClick={this.onExpand}>
              <Icon type={icons.arrowDown} className={!props.expanded ? style.depSpread : ''} />
            </span>
          }
          {props.checkable &&
            <Checkbox input={{ value: props.checked, onChange: this.onCheck }} />
          }
          <a
            className={style['vm-tree-node-content-wrapper']}
          >
            {
              this.state.dataLoading ? <span className={style['vm-tree-icon']}><Icon type={icons.loading} size={8} /></span> :
                (props.isShowIcon ? (props.isLeaf ? props.leafIcon && (<span className={style['vm-tree-icon']}>{props.leafIcon}</span>) :
                  (props.expanded ? (<span className={style['vm-tree-icon']}>{props.expandedIcon}</span>)
                  : (<span className={style['vm-tree-icon']}>{props.defaultIcon}</span>))) : null)
            }
            {
              props.tooltip && (
                <div
                  {...tip}
                  className={style['vm-tree-title-wrapper']}
                >
                  {content}
                </div>
              )
            }
            {
              !props.tooltip && (
              <div className={style['vm-tree-title-wrapper']}>
                {content}
              </div>)
            }
          </a>
        </div>
        {newChildren}
      </li>
    );
  }
}

TreeNode.propTypes = {
  className: React.PropTypes.string,
  title: React.PropTypes.string,
  value: React.PropTypes.string,
  disabled: React.PropTypes.bool,
  expanded: React.PropTypes.bool,
  isLeaf: React.PropTypes.bool,
  root: React.PropTypes.object,
  expandedIcon: React.PropTypes.element,
  defaultIcon: React.PropTypes.element,
  leafIcon: React.PropTypes.element,
  tooltip: React.PropTypes.bool,
  paddingLeft: React.PropTypes.number,
};

TreeNode.defaultProps = {
  paddingLeft: 15,
  expandedIcon: <Icon type={icons.folderOpen} size={16} color="#888" />,
  defaultIcon: <Icon type={icons.folder} size={16} color="#888" />,
};

TreeNode.isTreeNode = 1;

TreeNode.examples = {
  DefaultExample: {
    description: 'Default Example',
    component: <ul className={style['vm-tree']}>
      <TreeNode title="parent 1" key="0-0" />
    </ul>,
  },
  ChildrenExample: {
    description: 'Children Example',
    component: <ul className={style['vm-tree']}>
      <TreeNode title="parent 1" key="0-0" >
        <TreeNode title="Leaf 1" key="0-0-0" isLeaf />
        <TreeNode title="Leaf 2" key="0-0-1" isLeaf />
      </TreeNode>
      <TreeNode title="parent 2" key="0-1" />
    </ul>,
  },
};

export default TreeNode;



