import * as visitorDetails from '../constants/types/visitorDetails';

export const handleSubmitWrapup = id => ({
  type: visitorDetails.handleSubmitWrapup,
  meta: id,
  payload: {
    ifWrapupLoading: true,
    ifWrapupSubmitSuccess: false,
    wrapupSubmitError: '',
    showWrapupRequired: false,
  },
});
export const discardWrapup = id => ({
  type: visitorDetails.handleSubmitWrapup,
  meta: id,
  payload: {
    ifWrapupSubmitSuccess: false,
    wrapupSubmitError: '',
  },
});
export const wrapupHistoryLoading = (id, ifWrapupHistoryLoading) => ({
  type: visitorDetails.wrapupHistory,
  meta: id,
  payload: {
    ifWrapupHistoryLoading,
  },
});

export const wrapupDetailsOpen = (chatId, id) => ({
  type: visitorDetails.wrapupDetails,
  meta: chatId,
  payload: {
    wrapupDetailsOpenId: id,
  },
});



