import * as visitorDetails from '../constants/types/visitorDetails';

export const handleUpdateHistoryChatId = (visitorId, historyChatId) => ({
  type: visitorDetails.updateHistoryChatId,
  payload: historyChatId,
  meta: visitorId,
});

export const handleUpdateHistoryMessageId = (visitorId, messageId) => ({
  type: visitorDetails.updateHistoryMessageId,
  payload: messageId,
  meta: visitorId,
});

export const handleUpdateHistoryRefreshingStatus = (visitorId, status) => ({
  type: visitorDetails.updateHistoryRefreshingStatus,
  payload: status,
  meta: visitorId,
});

export const handleUpdateHistoryViewType = (visitorId, type) => ({
  type: visitorDetails.updateHistoryViewType,
  payload: type,
  meta: visitorId,
});

export const handleUdpateIsViewAllChats = (visitorId, status) => ({
  type: visitorDetails.updateIsViewAllChats,
  payload: status,
  meta: visitorId,
});

export const handleUdpateIsViewAllMessages = (visitorId, status) => ({
  type: visitorDetails.updateIsViewAllMessages,
  payload: status,
  meta: visitorId,
});



