import React from 'react';
import classnames from 'classnames';
import * as _ from 'lodash';
import InfoPrechat from './InfoPrechat';
import InfoCustomFields from './InfoCustomFields';
import InfoCustomVariables from './InfoCustomVariables';
import InfoVisitorSegment from './InfoVisitorSegment';
import Location from './VisitorLocation';
import WhereFrom from './WhereFrom';
import CurrentBrowsing from './CurrentBrowsing';
import InfoHistory from './InfoHistory';
import DeviceInfo from './DeviceInfo';
import AgentComment from './AgentComment';
import OsBrowserImage from './OsBrowserImage';
import style from './info.css';
import memoryStore from '../../../services/memoryStore';

class Info extends React.Component {
  constructor(props) {
    super(props);
    this.scrollTop = memoryStore.get('infoDivScrollTop') || 0;
  }
  componentDidMount() {
    if (this.scrollTop !== 0 && this.container) {
      this.container.scrollTop = this.scrollTop;
    }
  }
  shouldComponentUpdate(nextProps) {
    if (this.props.visitorId !== nextProps.visitorId || this.props.chatId !== nextProps.chatId) {
      return true;
    } else if (!_.isEqual(this.props.visitorInfo, nextProps.visitorInfo)) {
      return true;
    }
    return false;
  }
  componentDidUpdate(prevProps) {
    if (this.props.visitorId !== prevProps.visitorId || this.props.chatId !== prevProps.chatId) {
      memoryStore.set('infoDivScrollTop', 0);
      if (this.container) {
        this.container.scrollTop = 0;
      }
    }
  }
  componentWillUnmount() {
    let scrollT = 0;
    if (this.container) {
      scrollT = this.container.scrollTop;
    }
    memoryStore.set('infoDivScrollTop', scrollT);
  }

  render() {
    if (!this.props.visitorId) return null;
    if (!this.props.visitorInfo) return null;
    const { className, visitorInfo, onCurrentBrowsingIconClick, onChatTimesClick, ...rest } =
      this.props;
    return (
      <div
        className={classnames(style.info, className)}
        ref={(element) => { this.container = element; }}
      >
        <InfoPrechat {...visitorInfo} {...rest} />
        {visitorInfo.fieldsInfo && visitorInfo.fieldsInfo.length > 0 &&
        <InfoCustomFields {...visitorInfo} {...rest} />
        }
        {visitorInfo.customVariables && visitorInfo.customVariables.length > 0 && visitorInfo.ifEnableCustomVariables &&
        <InfoCustomVariables {...visitorInfo} />
        }
        {visitorInfo.ifEnableSegmentation &&
        <InfoVisitorSegment {...visitorInfo} />
        }
        <Location {...visitorInfo} />
        <WhereFrom {...visitorInfo} />
        { visitorInfo.currentPageUrl &&
        <CurrentBrowsing
          {...visitorInfo}
          onCurrentBrowsingIconClick={onCurrentBrowsingIconClick}
        />
        }
        <InfoHistory
          {...visitorInfo}
          onChatTimesClick={onChatTimesClick}
        />
        <DeviceInfo {...visitorInfo} />
        { !visitorInfo.ifEnableWrapupChat && !visitorInfo.readOnly &&
          <AgentComment {...visitorInfo} {...rest} />
        }
        { visitorInfo.ifOsBrowserImageShow &&
          <OsBrowserImage {...visitorInfo} />
        }
      </div>
    );
  }
}

Info.propTypes = {
  visitorId: React.PropTypes.number,
  chatId: React.PropTypes.number,
  visitorInfo: React.PropTypes.object,
  className: React.PropTypes.string,
  onCurrentBrowsingIconClick: React.PropTypes.func,
  onChatTimesClick: React.PropTypes.func,
};

export default Info;



