import React from 'react';
import ReactTooltip from 'react-tooltip';
import * as _ from 'lodash';
import style from './Wrapup.css';
import Link from '../../Link/Link';
import WrapupDetail from './WrapupDetail';
import * as timeUtil from '../../../utils/time';
import { icons } from '../../../constants/enums';
import Icon from '../../Icon/Icon';

const formatData = ({ sessionStartTime, agentNames, department, chatCategory }, timezone,timezoneoffset, datefmt) => {
  const nowDate = new Date();
  let result = '';
  const time = timeUtil.formatTimeForHistory(timezone,timezoneoffset, sessionStartTime, datefmt);
  let trimOperatorNames = _.trimEnd(agentNames);
  if (_.endsWith(trimOperatorNames, ',')) {
    trimOperatorNames = trimOperatorNames.substr(0, trimOperatorNames.length - 1);
  }
  result += `${time}      ${trimOperatorNames}`;
  if (department) result += `   |   ${department}`;
  if (chatCategory) result += `  |   ${chatCategory}`;
  return result;
};
class WrapupHistory extends React.Component {
  componentDidMount() {
    if (this.props.autoRefreshDataFlag) return;
    this.props.onRefreshWrapupHistory(this.props.chatId, this.props.visitorId);
  }
  shouldComponentUpdate(nextProps) {
    if (this.props.chatId !== nextProps.chatId ||
    this.props.ifWrapupHistoryLoading !== nextProps.ifWrapupHistoryLoading ||
    this.props.wrapupDetailsOpenId !== nextProps.wrapupDetailsOpenId ||
    !_.isEqual(this.props.wrapups, nextProps.wrapups)
    ) {
      return true;
    }
    return false;
  }
  componentDidUpdate() {
    if (!this.props.autoRefreshDataFlag) {
      this.props.onRefreshWrapupHistory(this.props.chatId, this.props.visitorId);
    }
    ReactTooltip.rebuild();
  }
  render() {
    const { wrapups, ifWrapupHistoryLoading, onRefreshWrapupHistory,
      onWrapupDetailsClick, wrapupDetailsOpenId, datefmt,timezone, timezoneoffset,
      ...rest
    } = this.props;
    return (<div className={style.WrapupHistoryContainer}>
      <div className={style.WrapupHistoryHeader}>
        <h4>Wrap-up History</h4>
        <Link
          onClick={(e) => {
            e.preventDefault();
            e.stopPropagation();
            onRefreshWrapupHistory(this.props.chatId, this.props.visitorId);
          }}
        >Refresh</Link>
      </div>
      { ifWrapupHistoryLoading &&
        (<div className={style.Loading}>
          Loading...
        </div>)
      }
      { wrapups.length > 0 &&
        (<ul className={style.List}>
          {
            wrapups.map((item, index) => (
              <li key={index}>
                <a
                  data-tip={formatData(item,timezone, timezoneoffset, datefmt)}
                  data-for="wrapupHistory"
                  className={style.historyItem}
                  onClick={
                    (e) => {
                      e.preventDefault();
                      e.stopPropagation();
                      onWrapupDetailsClick(index, item.id, this.props.chatId, this.props.visitorId);
                    }
                  }
                ><span>{formatData(item,timezone, timezoneoffset, datefmt)}</span></a>
                {
                  item.wrapupDetailsErrorString && index !== wrapupDetailsOpenId &&
                  <div className={style.Loading}>
                    <Icon
                      className={style.refreshIcon}
                      type={icons.refresh} color="#aaa" size={14}
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        onWrapupDetailsClick(index, item.id, this.props.chatId, this.props.visitorId);
                      }}
                    />
                    <span className={style.wrapupDetailsError}>{item.wrapupDetailsErrorString}</span>
                  </div>
                }
                {
                  index === wrapupDetailsOpenId &&
                  <WrapupDetail
                    className={style.WrapupDetail}
                    id={item.id}
                    ifOpen={index === wrapupDetailsOpenId}
                    sessionType={item.type}
                    sessionId={item.sessionId}
                    ifWrapupDetailsLoading={item.wrapupDetails && item.wrapupDetails.length > 0 ? false : item.ifWrapupDetailsLoading}
                    wrapupContent={item.wrapupDetails}
                    {...rest}
                  />
                }
              </li>
            ))
          }
        </ul>)
      }
    </div>);
  }
}

WrapupHistory.propTypes = {
  wrapups: React.PropTypes.arrayOf(React.PropTypes.shape({})),
  ifWrapupHistoryLoading: React.PropTypes.bool,
  onRefreshWrapupHistory: React.PropTypes.func,
  onWrapupDetailsClick: React.PropTypes.func,
  autoRefreshDataFlag: React.PropTypes.bool,
  wrapupDetailsOpenId: React.PropTypes.number,
};

export default WrapupHistory;



