import React, { PropTypes } from 'react';
import classnames from 'classnames';
import style from './salesforce.css';
import commonStyle from '../../CommonResources/common.css';

const AccountInfo = props =>
(<div className={style.accountinfo}>
  <div className={style.accountTitle}>Account Details</div>
  {props.salesforceAccountFields.map(
  (item, idx) => (<div key={idx} className={classnames(commonStyle.clearfix, style.fieldInfo)}>
    <span className={style.accountName}>{`${item.label} `}</span>
    <span className={style.accountValue}>{item.value}</span>
  </div>)
           )}
</div>);


AccountInfo.propTypes = {
  salesforceAccountFields: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    value: PropTypes.string,
    label: PropTypes.string,
    displayValue: PropTypes.string,
  })),
};

export default AccountInfo;



