import React from 'react';
import ReactTooltip from 'react-tooltip';
import * as _ from 'lodash';
import classnames from 'classnames';
import style from './CannedMessage.css';
import Input from '../../Input/Input';
import Link from '../../Link/Link';
import Icon from '../../Icon/Icon';
import { icons } from '../../../constants/enums';
import Tree from '../../Tree/Tree';
import TreeNode from '../../Tree/TreeNode';
import commonStyle from '../../CommonResources/common.css';
import memoryStore from '../../../services/memoryStore';
import FeatureHelp from '../../FeatureHelp/FeatureHelp';
import { shouldComponentUpdateGen } from '../../../utils/helper';
import HighLight from './HighLight';

const createCannedMessageTree = (cannedMessages, categories, ifPrivate) => {
  const prefix = ifPrivate ? 1 : 0;
  const nocateMessages = cannedMessages.filter(message => message.ifPrivate === ifPrivate && message.categoryId === 0);
  const temp = nocateMessages.map((mes, i) => <TreeNode key={`0-${prefix}-${i}`} id={mes.id} title={mes.title} value={mes.content} tooltip isLeaf />);
  const len = nocateMessages.length;
  const temp1 = createCannedMessageCategoryTree(cannedMessages, categories, ifPrivate, 0, `0-${prefix}`, len);
  const filterResult = temp1 ? temp1.filter(c => !!c) : [];
  if (filterResult.length > 0)
    return temp.concat(filterResult);
  return temp;
};

const createCannedMessageCategoryTree = (cannedMessages, categories, ifPrivate, parentId, key, start) => {
  const temp = categories.filter(item => item.ifPrivate === ifPrivate && item.parentId === parentId);
  if (temp.length === 0) return null;
  const temp1 = temp.map((category, index) => {
    const messages = cannedMessages.filter(message => message.ifPrivate === ifPrivate && message.categoryId === category.id);
    const children = createCannedMessageCategoryTree(cannedMessages, categories, ifPrivate, category.id, `${key}-${start + index}`, messages.length);
    const mess = messages.map((mes, i) => <TreeNode key={`${key}-${start + index}-${i}`} id={mes.id} title={mes.title} value={mes.content} tooltip isLeaf />);
    const filterChildren = children ? children.filter(c => !!c) : [];
    const result = mess.concat(filterChildren);
    if (result.length > 0)
      return (<TreeNode key={`${key}-${start + index}`} title={category.name} isShowIcon>
        {result}
      </TreeNode>);
    return (<TreeNode key={`${key}-${start + index}`} title={category.name} isShowIcon />)
  });
  return temp1;
};

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'CannedMessage Tab',
  ['newFeatureNotify', 'ifCannedMessageLoading', 'shouldFocus'],
  ['searchText', 'selectIndex']
);

class CannedMessage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      searchText: memoryStore.get('cannedMessage_searchText') || '',
      selectIndex: 0,
    };
    this.searchTextChanged = this.searchTextChanged.bind(this);
    this.closeIconClick = this.closeIconClick.bind(this);
    this.onClick = this.onClick.bind(this);
    this.onTreeNodeClick = this.onTreeNodeClick.bind(this);
    this.onTreeNodeDoubleClick = this.onTreeNodeDoubleClick.bind(this);
    this.onKeyDown = this.onKeyDown.bind(this);
    this.onFeatrueClick = this.onFeatrueClick.bind(this);
    this.onRefreshCannedMessage = this.onRefreshCannedMessage.bind(this);
    this.selectItem = null;
    this.searchCannedMeaasge = [];
    this.timer = null;
    this.shouldFocus = memoryStore.get('shouldFocusInput');
  }
  componentDidMount() {
    if (this.shouldFocus) {
      this.searchInput.focus();
      memoryStore.set('shouldFocusInput', false);
    }
    const sTop = memoryStore.get('cannedMessage_ScrollTop');
    if (sTop) this.scrollDiv.scrollTop = sTop;
  }

  shouldComponentUpdate(nextProps, nextState) {
    return shouldComponentUpdateHelper(this.props, nextProps, this.state, nextState);
  }
  componentDidUpdate(prevProps, prevState) {
    if (((!this.props.isChatting && this.state.selectIndex !== prevState.selectIndex)
      || this.props.shouldFocus) && this.searchInput) {
      this.searchInput.focus();
      if (this.props.shouldFocus) {
        memoryStore.set('shouldFocusInput', false);
      }
    }
    if ((_.trim(this.state.searchText) && !_.trim(prevState.searchText))
      || (!_.trim(this.state.searchText) && _.trim(prevState.searchText))) {
      this.scrollDiv.scrollTop = 0;
    }
    ReactTooltip.rebuild();
  }

  componentWillUnmount() {
    if (this.timer !== null) {
      clearTimeout(this.timer);
      this.timer = null;
    }
    memoryStore.set('cannedMessage_ScrollTop', this.scrollDiv.scrollTop);
  }
  onTreeNodeClick(id, value) {
    if (!this.props.isChatting) return;
    this.props.onClick(id, value, this.props.editorState);
  }
  onTreeNodeDoubleClick(id, value) {
    if (!this.props.isChatting) return;
    this.props.onDoubleClick(id, value);
  }
  onClick(index, id, content) {
    if (this.timer !== null) {
      clearTimeout(this.timer);
      this.timer = null;
      if (!this.props.isChatting) return;
      this.props.onDoubleClick(id, content);
    } else {
      const _this = this;
      this.timer = setTimeout(() => {
        _this.timer = null;
        if (_this.state.selectIndex !== index) {
          _this.setState({
            selectIndex: index,
          });
        }
        if (!_this.props.isChatting) return;
        _this.props.onClick(id, content, _this.props.editorState);
      }, 300);
    }
  }
  onKeyDown(event) {
    switch (event.nativeEvent.keyCode) {
      case 13:
        event.preventDefault();
        event.stopPropagation();
        if (this.selectItem) {
          if (!this.props.isChatting) return;
          if (this.searchInput) this.searchInput.blur();
          this.props.onClick(this.selectItem.id, this.selectItem.content, this.props.editorState);
        }
        break;
      case 27:
        event.preventDefault();
        event.stopPropagation();
        this.setState({
          searchText: '',
          selectIndex: 0,
        });
        break;
      case 38:
        event.preventDefault();
        event.stopPropagation();
        if (this.searchCannedMeaasge.length > 0 && this.state.selectIndex > 0) {
          this.setState({
            selectIndex: this.state.selectIndex - 1,
          });
          const element = document.getElementById(`searchItem_${this.state.selectIndex - 1}`);
          if (element) {
            if (element.scrollIntoViewIfNeeded) element.scrollIntoViewIfNeeded();
            else element.scrollIntoView(false);
          }
        }
        break;
      case 40:
        event.preventDefault();
        event.stopPropagation();
        if (this.searchCannedMeaasge.length > 0 && this.state.selectIndex < this.searchCannedMeaasge.length - 1) {
          this.setState({
            selectIndex: this.state.selectIndex + 1,
          });
          const element = document.getElementById(`searchItem_${this.state.selectIndex + 1}`);
          if (element) {
            if (element.scrollIntoViewIfNeeded) element.scrollIntoViewIfNeeded();
            else element.scrollIntoView(false);
          }
        }
        break;
      default:
        break;
    }
  }
  onFeatrueClick() {
    this.props.onFeatrueClick(this.props.newFeatureNotify + 4);
  }
  onRefreshCannedMessage() {
    this.props.onRefreshCannedMessage();
  }
  searchTextChanged(e) {
    memoryStore.set('cannedMessage_searchText', e.target.value);
    this.setState({
      searchText: e.target.value,
      selectIndex: 0,
    });
  }
  closeIconClick(event) {
    event.preventDefault();
    event.stopPropagation();
    memoryStore.set('cannedMessage_searchText', '');
    this.setState({
      searchText: '',
      selectIndex: 0,
    });
    this.searchInput.focus();
  }
  render() {
    const { categories, cannedMessages, ifCannedMessageLoading, newFeatureNotify } = this.props;
    const saveExpandedKeys = memoryStore.get('cannedMessage_expandedKeys', null, ['0-0']);
    this.searchCannedMeaasge = [];
    const text = _.trim(this.state.searchText);
    if (text) {
      this.searchCannedMeaasge = cannedMessages.filter((message) => {
        const pattStr = text.replace(/([.?*+^$[\]\\(){}|-])/g, '\\$1');
        const patt = new RegExp(pattStr, 'igm');
        return patt.test(message.title) || patt.test(message.content);
      });
      if (this.searchCannedMeaasge.length === 0) {
        this.selectItem = null;
      }
    } else {
      this.selectItem = null;
    }
    return (<div className={style.CannedMessageContainer}>
      <div className={style.container}>
        <div className={style.featrue}>
          <FeatureHelp
            ifShow={(newFeatureNotify & 4) == 0}
            text="Click a message to insert into the typing area or double click the message to send it out directly."
            featrueNumber={4}
            onGotitClick={this.onFeatrueClick}
          />
        </div>
        <Link onClick={this.onRefreshCannedMessage}>Refresh</Link>
        {ifCannedMessageLoading &&
          (<div className={style.Loading}>
            <Icon type={icons.loading} /> Loading...
          </div>)
        }
      </div>
      { !ifCannedMessageLoading &&
        (<div>
          <div className={classnames(style.container, style.searchInput)}>
            <Input
              inputRef={(input) => { this.searchInput = input; }}
              maxLength={512}
              type="text"
              className={style.inputWidth}
              input={{ value: this.state.searchText, onChange: this.searchTextChanged }}
              meta={{}}
              placeholder="Search"
              onKeyDown={this.onKeyDown}
            />
            <Icon className={classnames(style.closeIcon, text && style.visible)} type={icons.close} color="#666" size={10} onMouseDown={this.closeIconClick} />
          </div>
          <div ref={(element) => { this.scrollDiv = element; }} className={style.contentWrapper} style={{ top: (newFeatureNotify & 4) == 0 ? 170 : 80 }}>
            { !text && (
            <Tree
              className={style.tree}
              defaultExpandedKeys={saveExpandedKeys}
              onSelect={this.onTreeNodeClick}
              onDoubleClick={this.onTreeNodeDoubleClick}
              onExpand={(expandedKeys) => {
                memoryStore.set('cannedMessage_expandedKeys', expandedKeys);
              }}
            >
              <TreeNode title="Public" isShowIcon>
                {
                  createCannedMessageTree(cannedMessages, categories, false)
                }
              </TreeNode>
              <TreeNode title="Private" isShowIcon>
                {
                  createCannedMessageTree(cannedMessages, categories, true)
                }
              </TreeNode>
            </Tree>
          )
        }
            {
          text &&
          (<div className={style.search}>
            {
              this.searchCannedMeaasge.length > 0 ?
              this.searchCannedMeaasge.map((item, index) => {
                const pattStr = text.replace(/([.?*+^$[\]\\(){}|-])/g, '\\$1');
                const patt = new RegExp(`(${pattStr})`, 'igm');
                let tempTitle = item.title;
                let tempContent = item.content;
                let i = 0;
                let result;
                if ((result = patt.exec(item.title)) != null) {
                  i = patt.lastIndex;
                  if (result.length > 0 && item.title.length > 100) {
                    if (i > 20) {
                      tempTitle = `...${item.title.substring(i - 20, i)}${tempTitle.substring(i + 1)}`;
                    }
                  }
                }
                i = 0;
                if ((result = patt.exec(item.content)) != null) {
                  i = patt.lastIndex;
                  if (result.length > 0 && item.content.length > 100) {
                    if (i > 20) {
                      tempContent = `...${item.content.substring(i - 20, i)}${tempContent.substring(i + 1)}`;
                    }
                  }
                }
                // const title = htmlEncode(tempTitle).replace(patt, "<span style='background: #ffff00'>$1</span>");
                // const content = htmlEncode(tempContent).replace(patt, "<span style='background: #ffff00'>$1</span>");
                if (index === this.state.selectIndex) this.selectItem = item;
                return (
                  <div
                    key={index}
                    id={`searchItem_${index}`}
                    className={classnames(style.searchItem, index === this.state.selectIndex && style.select)}
                    onClick={() => {
                      this.onClick(index, item.id, item.content);
                    }}
                    data-tip={item.content}
                    data-for="vmtree"
                  >
                    <div className={style.title}>
                      <HighLight text={tempTitle} patt={patt} />
                    </div>
                    <div className={style.content}>
                      <HighLight text={tempContent} patt={patt} />
                    </div>
                  </div>
                );
              })
              : (<div className={style.noSearchResult}>No results were found.</div>)
            }
          </div>)
          }
          </div>
        </div>)
      }
      <ReactTooltip
        id="vmtree"
        effect="solid"
        place="left"
        multiline
        class={classnames(style.tooltip, commonStyle.tooltipInfo)}
      />
    </div>);
  }
}

CannedMessage.propTypes = {
  categories: React.PropTypes.arrayOf(React.PropTypes.shape({})),
  cannedMessages: React.PropTypes.arrayOf(React.PropTypes.shape({})),
  onRefreshCannedMessage: React.PropTypes.func,
  ifCannedMessageLoading: React.PropTypes.bool,
  onClick: React.PropTypes.func,
  onDoubleClick: React.PropTypes.func,
  isChatting: React.PropTypes.bool,
  newFeatureNotify: React.PropTypes.number,
  onFeatrueClick: React.PropTypes.func,
  editorState: React.PropTypes.shape({ }),
};

export default CannedMessage;



