import { connect } from 'react-redux';
import Navigation from '../components/Tabs/Navigation/Navigation';
import * as web from '../actions/web';
import * as selectors from '../reducers/selectors';
import * as actions from '../actions/navigation';
import * as enumPage from '../constants/enums/page';
import { parseDate } from '../utils/time';

const createNavigationProps = (state) => {
  const page = selectors.getPage(state);
  const ifNavigationLoading = selectors.getIfNavigationLoading(state);
  const timezoneoffset = selectors.getTimeDelay(state);
  let visitorId;
  if (page === enumPage.chats) {
    const chat = selectors.getSelectedChat(state);
    if (typeof chat === 'undefined') {
      return { navigationItems: [], visitorId: 0, ifNavigationLoading, timezoneoffset };
    }
    visitorId = chat.get('visitorId');
  } else {
    visitorId = selectors.getSelectedVisitorId(state);
  }
  const visitor = selectors.getVisitor(state, visitorId);
  if (!visitor) {
    return { navigationItems: [], visitorId: 0, chatId: 0, ifNavigationLoading, timezoneoffset };
  }
  const navigations = (visitor.get('navigation') || []).sort((a, b) => b.time - a.time);
  const navigationItems = navigations.map((item, i) => ({
    id: i,
    isCurrentPage: (i === 0),
    pageURL: item.pageUrl,
    title: item.title,
    time: parseDate(item.time),
  }));
  return { navigationItems, visitorId, ifNavigationLoading, timezoneoffset };
};

const mapDispatchToProps = dispatch => ({
  refreshNavigation(visitorId) {
    dispatch(actions.handleUpdateIfNavigationLoading(true));
    dispatch(actions.handleNavigationRefreshing(visitorId));
    dispatch(web.getNavigation(visitorId));
  },
});


export default connect(createNavigationProps, mapDispatchToProps)(Navigation);



