import React from 'react';
import * as _ from 'lodash';
import WrapupForm from '../../Form/WrapupForm';
import style from './Wrapup.css';
import * as enums from '../../../constants/enums';
import Icon from '../../Icon/Icon';

class WrapupFormWrapper extends React.Component {
  shouldComponentUpdate(nextProps) {
    if (this.props.ifWrapupLoading !== nextProps.ifWrapupLoading ||
    this.props.chatId !== nextProps.chatId ||
    this.props.disableWrapupForm !== nextProps.disableWrapupForm ||
    this.props.ifWrapupSubmitSuccess !== nextProps.ifWrapupSubmitSuccess ||
    this.props.showWrapupRequired !== nextProps.showWrapupRequired ||
    this.props.wrapupSubmitError !== nextProps.wrapupSubmitError) {
      return true;
    }
    return false;
  }
  render() {
    const { chatId, wrapupFields, handleSubmitWrapup, onDiscardWrapup, ifWrapupLoading, disableWrapupForm } = this.props;
    if (wrapupFields.length > 0) {
      return (
        <div className={style.wrapupFormWrapper}>
          <div id="errorMessageDiv">
            <div className={style.errorMessage} style={{ display: !this.props.ifWrapupSubmitSuccess && this.props.wrapupSubmitError ? 'block' : 'none' }}>
              <span>{this.props.wrapupSubmitError}</span>
            </div>
            <div className={style.required} style={{ display: this.props.showWrapupRequired ? 'block' : 'none' }}>
              <span>Please complete the Wrap-up form below.</span>
            </div>
            <div className={style.successMessage} style={{ display: this.props.ifWrapupSubmitSuccess ? 'block' : 'none' }}>
              <span><Icon type={enums.icons.success} />Wrap-up saved successfully.</span>
            </div>
          </div>
          <WrapupForm
            className={style.Container}
            fields={wrapupFields}
            fieldsValue={[]}
            chatId={chatId}
            onSubmit={handleSubmitWrapup}
            onCancel={onDiscardWrapup}
            ifSubmitLoading={ifWrapupLoading}
            disable={disableWrapupForm}
            ifSubmitSuccess={this.props.ifWrapupSubmitSuccess}
            ifEnableAdvancedCategoryMode={this.props.ifEnableAdvancedCategoryMode}
          />
        </div>);
    } else {
      return (<div className={style.nowrapupForm}>
      No wrap-up form for this chat. You can contact the account administrator to set up the wrap-up form.
      </div>);
    }
  }
}

WrapupFormWrapper.propTypes = {
  chatId: React.PropTypes.number,
  wrapupFields: React.PropTypes.arrayOf(React.PropTypes.shape({
    displayText: React.PropTypes.string,
    ifRequired: React.PropTypes.bool,
    options: React.PropTypes.array,
    type: React.PropTypes.number,
    id: React.PropTypes.number,
  })).isRequired,
  handleSubmitWrapup: React.PropTypes.func,
  ifWrapupLoading: React.PropTypes.bool,
  disableWrapupForm: React.PropTypes.bool,
  ifWrapupSubmitSuccess: React.PropTypes.bool,
  ifEnableAdvancedCategoryMode: React.PropTypes.bool,
  wrapupSubmitError: React.PropTypes.string,
  resetWrapupForm: React.PropTypes.func,
  showWrapupRequired: React.PropTypes.bool,
  onDiscardWrapup: React.PropTypes.func,
};

export default WrapupFormWrapper;



