import { connect } from 'react-redux';
import * as perf from 'src/utils/perf';
import CannedMessage from '../components/Tabs/CannedMessage/CannedMessage';
import * as selectors from '../reducers/selectors';
import * as agentActions from '../actions/agent';
import * as uiActions from '../actions/ui';
import memoryStore from '../services/memoryStore';
import { replaceMacros, isSendingMessageAllowed } from '../utils/common';
import * as configActions from '../actions/config';

const isSendingMessageToSelectedVisitorAllowed = (state) => {
  const chat = selectors.getSelectedChat(state);
  const visitor = selectors.getVisitorByChatId(state, chat && chat.get('id'));
  const currentAgentId = selectors.getCurrentAgentId(state);
  return isSendingMessageAllowed(visitor, currentAgentId);
};

let mapStateToProps = (state) => {
  const chat = selectors.getSelectedChat(state);
  const chatId = chat.get('id');
  const editorState = selectors.getChatEditorState(state, chatId);
  const visitorId = selectors.getVisitorIdByChatId(state, chatId);
  const visitor = selectors.getVisitor(state, visitorId);
  const ifCannedMessageLoading = state.ui.mychats.ifCannedMessageLoading;
  const categories = state.config.settings.categories;
  const cannedMessages = state.config.settings.cannedMessages;
  const newFeatureNotify = state.config.preference.newFeatureNotify;
  const agent = selectors.getCurrentAgent(state);
  const shouldFocus = memoryStore.get('shouldFocusInput');
  return {
    categories,
    cannedMessages,
    chatId,
    visitorId,
    visitor,
    isChatting: isSendingMessageToSelectedVisitorAllowed(state),
    ifCannedMessageLoading,
    newFeatureNotify,
    agent,
    shouldFocus,
    editorState,
  };
};

if (process.env.NODE_ENV !== 'production') {
  mapStateToProps = perf.markFunctionCall('CannedMessage: mapStateToProps', mapStateToProps);
}

let mergeProps = (stateProps, dispatchProps) => {
  const { dispatch } = dispatchProps;
  const dispatchObject = {
    onRefreshCannedMessage() {
      memoryStore.set('cannedMessage_expandedKeys', ['0-0']);
      dispatch(uiActions.refreshCannedMessage());
      dispatch(agentActions.getCannedMessages());
    },
    onClick(id, value, editorState) {
      const realValue = replaceMacros(value, stateProps.visitor, stateProps.agent);
      require.ensure(['src/actions/chatEditor'], (require) => {
        const { insertContent } = require('src/actions/chatEditor');

        dispatch(insertContent(stateProps.chatId, realValue, editorState));
      }, 'editor');
      dispatch(agentActions.agentUseCannedMessage(stateProps.chatId, id));
    },
    onDoubleClick(id, value) {
      const realValue = replaceMacros(value, stateProps.visitor, stateProps.agent);
      dispatch(agentActions.sendText(stateProps.visitorId, realValue));
      dispatch(agentActions.agentUseCannedMessage(stateProps.chatId, id));
    },
    onFeatrueClick(newFeature) {
      dispatch(configActions.updatePreference({ newFeatureNotify: newFeature }));
      dispatch(agentActions.setPreference());
    },
  };
  return {
    ...stateProps,
    ...dispatchObject,
  };
};

if (process.env.NODE_ENV !== 'production') {
  mergeProps = perf.markFunctionCall('CannedMessage: mergeProps', mergeProps);
}

export default connect(mapStateToProps, null, mergeProps)(CannedMessage);



