import { connect } from 'react-redux';
import * as _ from 'lodash';
import Wrapup from '../components/Tabs/Wrapup/Wrapup';
import * as agentActions from '../actions/agent';
import * as fieldType from '../constants/enums/fieldType';
import * as wrapupActions from '../actions/wrapup';
import * as configActions from '../actions/config';
import { createWrapupProps } from './Business/visitorDetailsTab';

const mapStateToProps = (state) => {
  return createWrapupProps(state);
}
const mergeProps = (stateProps, dispatchProps, ownProps) => {
  const { dispatch } = dispatchProps;
  const dispatchObject = {
    handleSubmitWrapup(form) {
      dispatch(wrapupActions.handleSubmitWrapup(stateProps.chatId));
      const fields = stateProps.wrapupFields.map((field) => {
        const temp = {};
        const name = `${field.displayText}_${field.id}`;
        if (field.type === fieldType.WrapupCategory) {
          if (stateProps.ifEnableAdvancedCategoryMode) {
            temp.categoryList = form[field.id];
          } else {
            temp.categoryId = form[name];
          }
        } else if (field.type === fieldType.WrapupComment) {
          temp.comment = form[name];
        } else {
          let value = form[name];
          if (field.type === fieldType.CheckBoxList) {
            if (value) {
              value = _.uniq(field.options.filter(option => _.indexOf(value.split(''), option.id.toString()) !== -1).map(option => option.displayText)).join('');
            } else {
              value = '';
            }
          }
          if (field.type === fieldType.DropDownList || field.type === fieldType.Radio) {
            if (value) {
              const opt = _.find(field.options, option => option.id.toString() === value);
              value = opt.displayText;
            } else {
              value = '';
            }
          }
          temp.customFields = { id: field.id,
            name: field.displayText,
            value: typeof value === 'undefined' ? '' : value };
        }
        return temp;
      });
      let wrapup = {};
      const customFields = [];
      _.each(fields, (item) => {
        if (item.customFields) {
          customFields.push(item.customFields);
        } else {
          wrapup = Object.assign(wrapup, item);
        }
      });
      wrapup.customFields = customFields;
      wrapup.chatId = stateProps.chatId;
      wrapup.visitorId = stateProps.visitorId;
      dispatch(agentActions.setWrapupForm(stateProps.visitorId, wrapup));
    },
    onDiscardWrapup() {
      dispatch(wrapupActions.discardWrapup(stateProps.chatId));
    },
    onRefreshWrapupHistory(chatId, visitorId) {
      dispatch(wrapupActions.wrapupHistoryLoading(chatId, true));
      dispatch(agentActions.getWrapupHistory(visitorId));
    },
    onWrapupDetailsClick(id, chatId, ownChatId, visitorId) {
      dispatch(wrapupActions.wrapupDetailsOpen(ownChatId, id));
      dispatch(agentActions.getWrapupDetails(visitorId, chatId));
    },
    onFeatrueClick(newFeature) {
      dispatch(configActions.updatePreference({ newFeatureNotify: newFeature }));
      dispatch(agentActions.setPreference());
    },
  };
  return {
    ...ownProps,
    ...stateProps,
    ...dispatchObject,
  };
};

export default connect(mapStateToProps, null, mergeProps)(Wrapup);



