import React, { PropTypes } from 'react';
import classnames from 'classnames';
import { icons as enums } from '../../../constants/enums';
import { contactInfo, LeadInfo } from '../../../constants/enums/salesforceInfoType';
import Icon from '../../Icon/Icon';
import style from './salesforce.css';
import commonStyle from '../../CommonResources/common.css';

const SalesforceInfo = props =>
  (
    <div className={classnames(style.Container, style.editable, style.notEdit)} >
      <span className={style.editIconWrapper}>
        <Icon
          type={enums.typing} color="#aaa" size={14}
          className={style.editIcon}
          onClick={() => { props.editInfo(props.infoType); }}
        />
      </span>


      {
        (
          <div>
            <div className={style.accountTitle}>
              {props.infoType === contactInfo ? 'Contact' : 'Lead' } Details</div>
            {props.Fields.map(
            (item, idx) => (<div key={idx} className={classnames(commonStyle.clearfix, style.fieldInfo)}>
              <span className={style.leadInfo}>{`${item.label} `}</span>
              <span className={style.leadValue}>{item.value}</span>
            </div>)
           )}
          </div>
        )
      }
    </div>
  );


SalesforceInfo.propTypes = {
  infoType: PropTypes.oneOf([contactInfo,
    LeadInfo]).isRequired,
  Fields: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    value: PropTypes.string,
    label: PropTypes.string,
    displayValue: PropTypes.string,
  })),

  editInfo: PropTypes.func.isRequired,
};

export default SalesforceInfo;



