import React, { PropTypes } from 'react';
import classnames from 'classnames';
import _ from 'lodash';
import style from './visitorHistory.css';
import Link from '../../Link/Link';

const HistoryMessageDetals = props => (
  <div>
    {props.refreshing ? (<div className={style.detailLoading}>
      Loading...
    </div>) :
      (
        <div className={style.detail}>
          { _.isArray(props.offLineMessage.customVariables) &&
        (<div>
          <div className={style.title}>Custom Variable:</div>
          <div className={classnames(style.information, 'contentMenu')}>
            {
            props.offLineMessage.customVariables.map(item => (
              <div key={`visitorHistoryChat_${item.name}`}>
                <span>{item.name}: </span>
                {item.value && item.url &&
                  <Link newWindow href={item.url}>{item.value}</Link>
                }
                {item.value && !item.url &&
                  <span>{item.value}</span>
                }
              </div>
            ))
          }
          </div>
          <div className={style.splitLine} />
          </div>)}
          <div className={style.title}>Content:</div>
          <div className={classnames(style.information, 'contentMenu')}>{props.offLineMessage.content}</div>
        </div>
      )}
  </div>
);

HistoryMessageDetals.propTypes = {
  refreshing: PropTypes.bool.isRequired,
  offLineMessage: PropTypes.shape({
    messageId: PropTypes.number,
    content: PropTypes.string,
    customVariables: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string.isRequired,
      value: PropTypes.string.isRequired,
    })).isRequired,
  }),
};

export default HistoryMessageDetals;



