import React from 'react';
import classnames from 'classnames';
import * as _ from 'lodash';
import ReactTooltip from 'react-tooltip';
import WrapupForm from './WrapupFormWrapper';
import WrapupHistory from './WrapupHistory';
import style from './Wrapup.css';
import FeatureHelp from '../../FeatureHelp/FeatureHelp';
import memoryStore from '../../../services/memoryStore';

class Wrapup extends React.Component {
  componentDidMount() {
    const t = memoryStore.get('wrapupDivScrollTop');
    if (t && this.container) {
      this.container.scrollTop = t;
    }
  }
  shouldComponentUpdate(nextProps) {
    if (this.props.chatId !== nextProps.chatId) {
      return true;
    } else if (!_.isEqual(this.props.wrapups, nextProps.wrapups) ||
      this.props.disableWrapupForm !== nextProps.disableWrapupForm ||
      this.props.ifWrapupLoading !== nextProps.ifWrapupLoading ||
      this.props.ifWrapupSubmitSuccess !== nextProps.ifWrapupSubmitSuccess ||
      this.props.wrapupSubmitError !== nextProps.wrapupSubmitError ||
      this.props.ifWrapupHistoryLoading !== nextProps.ifWrapupHistoryLoading ||
      this.props.autoRefreshDataFlag !== nextProps.autoRefreshDataFlag ||
      this.props.wrapupDetailsOpenId !== nextProps.wrapupDetailsOpenId ||
      this.props.newFeatureNotify !== nextProps.newFeatureNotify ||
      this.props.showWrapupRequired !== nextProps.showWrapupRequired) {
      return true;
    }
    return false;
  }
  componentDidUpdate(prevProps) {
    if (this.props.chatId !== prevProps.chatId) {
      memoryStore.set('wrapupDivScrollTop', 0);
      if (this.container) {
        this.container.scrollTop = 0;
      }
    } else if (prevProps.ifWrapupLoading && !this.props.ifWrapupLoading &&
    (this.props.ifWrapupSubmitSuccess || (!this.props.ifWrapupSubmitSuccess && this.props.wrapupSubmitError))) {
      if (this.container) {
        this.container.scrollTop = 0;
      }
    }
  }
  componentWillUnmount() {
    if (this.container) {
      memoryStore.set('wrapupDivScrollTop', this.container.scrollTop);
    }
  }
  render() {
    if (!this.props.chatId) return null;
    const { className, newFeatureNotify, onFeatrueClick, ...rest } = this.props;
    return (
      <div ref={(element) => { this.container = element; }} className={style.wrapupWrapper}>
        <div className={classnames(style.wrapup, className)}>
          <div>
            <div className={style.featrue}>
              <FeatureHelp
                ifShow={(newFeatureNotify & 8) == 0}
                text="Fill out the wrap-up form to save time locating, organizing and reviewing your chats."
                featrueNumber={8}
                onGotitClick={
                () => {
                  if (onFeatrueClick) onFeatrueClick(newFeatureNotify + 8);
                }}
              />
            </div>
            <WrapupForm {...rest} />
          </div>
          <WrapupHistory {...rest} />
        </div>
        <ReactTooltip
          id="wrapupHistory"
          effect="solid"
          place="bottom"
          multiline
          class={style.tooltip}
        />
      </div>
    );
  }
}

Wrapup.propTypes = {
  className: React.PropTypes.string,
  chatId: React.PropTypes.number,
  wrapupFields: React.PropTypes.arrayOf(React.PropTypes.shape({
    displayText: React.PropTypes.string,
    ifRequired: React.PropTypes.bool,
    options: React.PropTypes.array,
    type: React.PropTypes.number,
    id: React.PropTypes.number,
  })).isRequired,
  handleSubmitWrapup: React.PropTypes.func,
  ifWrapupLoading: React.PropTypes.bool,
  ifEnableAdvancedCategoryMode: React.PropTypes.bool,
  disableWrapupForm: React.PropTypes.bool,
  ifWrapupSubmitSuccess: React.PropTypes.bool,
  wrapupSubmitError: React.PropTypes.string,
  resetWrapupForm: React.PropTypes.func,
  showWrapupRequired: React.PropTypes.bool,
  wrapups: React.PropTypes.arrayOf(React.PropTypes.shape({})),
  ifWrapupHistoryLoading: React.PropTypes.bool,
  onRefreshWrapupHistory: React.PropTypes.func,
  onWrapupDetailsClick: React.PropTypes.func,
  autoRefreshDataFlag: React.PropTypes.bool,
  wrapupDetailsOpenId: React.PropTypes.number,
  newFeatureNotify: React.PropTypes.number,
  onFeatrueClick: React.PropTypes.func,
};

export default Wrapup;



