import React from 'react';
import classnames from 'classnames';
import style from './info.css';

const getLocation = (country, state, city, ip) => {
  let result = '';
  if (city && city !== '-') {
    result += `${city}, `;
  }
  if (state && state !== '-') {
    result += `${state}, `;
  }
  if (country && country !== '-') {
    result += `${country}, `;
  }
  if (result.length > 0) {
    result = result.substring(0, result.length - 2);
    if (ip) {
      result += `(${ip})`;
    }
  } else {
    result = ip;
  }
  return result;
};

class VisitorLocation extends React.Component {
  shouldComponentUpdate(nextProps) {
    if (this.props.country !== nextProps.country ||
    this.props.state !== nextProps.state ||
    this.props.city !== nextProps.city ||
    this.props.ip !== nextProps.ip) {
      return true;
    }
    return false;
  }
  render() {
    const { country, state, city, ip } = this.props;
    const location = getLocation(country, state, city, ip);
    if (location) {
      return (
        <div className={classnames(style.Container, style.notEdit)}>
          <div className={style.title}>Location</div>
          <div className={classnames(style.line, 'contentMenu')} tabIndex={-1}>
            {location}
          </div>
        </div>); }
    return null;
  }
}

VisitorLocation.propTypes = {
  country: React.PropTypes.string,
  state: React.PropTypes.string,
  city: React.PropTypes.string,
  ip: React.PropTypes.string,
};

VisitorLocation.examples = {
  'Default Example': {
    description: 'Default Example',
    component:
      <VisitorLocation ip="123.22.2.2" country="China" state="Hunan" city="Changsha" />,
  },
};

export default VisitorLocation;



