import React, { PropTypes, Component } from 'react';
import * as _ from 'lodash';
import classnames from 'classnames';
import SalesforceLookup from './SalesforceLookup';
import Modal from '../Modal';
import lookupStyle from './salesforceLookup.css';
import { icons as enums } from '../../../constants/enums';
import Icon from '../../Icon/Icon';


class SalesforceLookupModel extends Component {


  constructor(props) {
    super(props);
    this.state = {
      isLookupOpen: false,
      searchType: 1,
    };
    this.onClose = this.onClose.bind(this);
    this.onItemSelected = this.onItemSelected.bind(this);
    this.handlerChange = this.handlerChange.bind(this);
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (!_.isEqual(this.props.selectOptions, nextProps.selectOptions) ||
      !_.isEqual(this.props.input.value, nextProps.input.value) ||
      !_.isEqual(this.props.meta, nextProps.meta) ||
      this.state.isLookupOpen !== nextState.isLookupOpen ||
      this.state.searchType !== nextState.searchType) {
      return true;
    }
    return false;
  }
  onClose() {
    this.setState({ isLookupOpen: false });
  }
  onItemSelected(id, val) {
    const result = { id, value: val };
    if (!_.isEqual(this.props.input.value, result)) this.props.input.onChange(result);
  }
  handlerChange(e) {
    const result = { id: e.target.value, value: e.target.value };
    if (!_.isEqual(this.props.input.value, result)) this.props.input.onChange(result);
  }


  render() {
    return (
      <div>
        <div>
          <span className={lookupStyle.relativePosition} tabIndex="0">
            {this.props.isNeedSelector && <Icon
              type={enums.search}
              className={lookupStyle.searchIcon}
              onClick={() => this.setState({ isLookupOpen: true })}
            />
           }
            <input
              // placeholder={this.props.isNeedSelector ? 'please select' : ''}
              readOnly={this.props.isNeedSelector}
              tabIndex="-1"
              type="text"
              className={classnames(
                      lookupStyle.input,
                      lookupStyle.inputStyle,
                      this.props.meta.touched && this.props.meta.invalid && lookupStyle.error
                    )}
              value={this.props.input.value.value || ''}
              onChange={this.props.isNeedSelector ? undefined : this.handlerChange}
            />
          </span>
        </div>
        {
          this.state.isLookupOpen && <Modal
            closeModal={this.onClose}
            className={lookupStyle.lookupWin}
            ifShowCloseIcon
          >
            {React.createElement(SalesforceLookup, {
              closeModal: this.onClose,
              selectOptions: this.props.selectOptions,
              onItemSelected: this.onItemSelected,
              onSalesforcelookUp: this.props.onSalesforcelookUp,
            })}
          </Modal>
        }
      </div>
    );
  }
}
SalesforceLookupModel.propTypes = {
  selectOptions: PropTypes.arrayOf(PropTypes.string).isRequired,
  input: PropTypes.shape({
    value: PropTypes.shape({
      id: PropTypes.string,
      value: PropTypes.string,
    }),
    onChange: PropTypes.func,
  }).isRequired,
  onSalesforcelookUp: PropTypes.func.isRequired,
  isNeedSelector: PropTypes.bool,
  inputStyle: PropTypes.string,
  meta: PropTypes.object,
};

export default SalesforceLookupModel;



