import * as chatAction from '../constants/enums/chatAction';
import * as salesforce from '../constants/types/visitorDetails';
import { createWebRequest } from './web';

export const agentGetSFIntegrationConfig = siteId =>
  createWebRequest(chatAction.agentGetSFIntegrationConfig, siteId);
export const agentGetSFContactFields = siteId =>
  createWebRequest(chatAction.agentGetSFContactFields, siteId);
export const agentGetSFAccountFields = siteId =>
  createWebRequest(chatAction.agentGetSFAccountFields, siteId);
export const agentGetSFLeadFields = siteId =>
  createWebRequest(chatAction.agentGetSFLeadFields, siteId);

export const agentCreateSFContact = (visitorId, fieldValues) =>
  createWebRequest(chatAction.agentCreateSFContact, { visitorId, fieldValues });
export const agentEditSFContact = (visitorId, fieldValues) =>
  createWebRequest(chatAction.agentEditSFContact, { visitorId, fieldValues });

export const agentCreateSFLead = (visitorId, fieldValues) =>
  createWebRequest(chatAction.agentCreateSFLead, { visitorId, fieldValues });
export const agentEditSFLead = (visitorId, fieldValues) =>
  createWebRequest(chatAction.agentEditSFLead, { visitorId, fieldValues });

export const agentCreateSFCase = visitorId =>
  createWebRequest(chatAction.agentCreateSFCase, visitorId);
export const agentAttachSFCaseToContact = (visitorId, contactId) =>
  createWebRequest(chatAction.agentAttachSFCaseToContact, { visitorId, contactId });
export const agentAttachSFTaskToLead = (visitorId, leadId) =>
  createWebRequest(chatAction.agentAttachSFTaskToLead, { visitorId, leadId });

export const agentLookupSFObject = (type, keyword, next) =>
  createWebRequest(chatAction.agentLookupSFObject, { type, keyword }, next);

export const agentCheckSFAutoIntegrationResult = visitorId =>
  createWebRequest(chatAction.agentCheckSFAutoIntegrationResult, visitorId);

export const onCancelClick = () => ({
  type: salesforce.updateSalesforce,
  payload: {
    showForm: false,
  },
});
export const onEditOrCreate = type => ({
  type: salesforce.updateSalesforce,
  payload: {
    showForm: true,
    editType: type,
    ifSalesforceLoading: false,
    salesforceSubmitError: '',
  },
});

export const reset = () => ({
  type: salesforce.updateSalesforce,
  payload: {
    showForm: false,
    editType: 0,
    ifSalesforceLoading: false,
    salesforceSubmitError: '',
    waiting: false,
    errorMessage: '',
  },
});

export const handleSubmitSalesforce = () => ({
  type: salesforce.updateSalesforce,
  payload: {
    ifSalesforceLoading: true,
    salesforceSubmitError: '',
  },
});

export const handleAttach = () => ({
  type: salesforce.updateSalesforce,
  payload: {
    waiting: true,
    errorMessage: '',
  },
});



