import { connect } from 'react-redux';
import * as _ from 'lodash';
import Info from '../components/Tabs/Info/Info';
import * as infoActions from '../actions/visitorDetails';
import * as configActions from '../actions/config';
import { visitorDetailsTabs } from '../constants/enums';
import * as agentActions from '../actions/agent';
import { createInfoProps } from './Business/visitorDetailsTab';
import * as fieldType from '../constants/enums/fieldType';

const mapStateToProps = (state) => {
  return createInfoProps(state);
}

const mergeProps = (stateProps, dispatchProps, ownProps) => {
  const { dispatch } = dispatchProps;
  const dispatchObject = {
    onPreChatEditIconClick() {
      dispatch(infoActions.onPreChatEditIconClick());
    },
    onPreChatCancelClick() {
      dispatch(infoActions.onPreChatCancelClick());
    },
    onCustomFieldEditIconClick() {
      dispatch(infoActions.onCustomFieldEditIconClick());
    },
    onCustomFieldCancelClick() {
      dispatch(infoActions.onCustomFieldCancelClick());
    },
    onCurrentBrowsingIconClick(flag) {
      dispatch(infoActions.onCurrentBrowsingIconClick(flag));
    },
    onCommentEditIconClick() {
      dispatch(infoActions.onCommentEditIconClick());
    },
    onCommentCancelClick() {
      dispatch(infoActions.onCommentCancelClick());
    },
    onChatTimesClick() {
      dispatch(infoActions.onDetailsTabSelect(visitorDetailsTabs.history));
    },
    handleSubmitpreChat(preChat) {
      dispatch(infoActions.handleSubmitpreChat());
      dispatch(agentActions.setPreChatForm(stateProps.visitorId, preChat));
    },
    handleSubmitCustomFields(form) {
      dispatch(infoActions.handleSubmitCustomFields());
      const fields = stateProps.visitorInfo.fieldsInfo.map((field) => {
        let value = form[`${field.displayText}_${field.id}`];
        if (field.type === fieldType.CheckBoxList) {
          if (value) {
            value = _.uniq(field.options.filter(option => _.indexOf(value.split(''), option.id.toString()) !== -1).map(option => option.displayText)).join('');
          } else {
            value = '';
          }
        }
        if (field.type === fieldType.DropDownList || field.type === fieldType.Radio) {
          if (value) {
            const opt = _.find(field.options, option => option.id.toString() === value);
            value = opt.displayText;
          } else {
            value = '';
          }
        }
        return {
          id: field.id,
          name: field.displayText,
          value,
        };
      });
      dispatch(agentActions.setCustomFields(stateProps.visitorId, fields));
    },
    handleSubmitComment(form) {
      dispatch(infoActions.handleSubmitComment());
      dispatch(agentActions.setAgentComment(stateProps.visitorId, form.comment));
    },
    onFeatrueClick(newFeature) {
      dispatch(configActions.updatePreference({ newFeatureNotify: newFeature }));
      dispatch(agentActions.setPreference());
    },
  };
  return {
    ...ownProps,
    ...stateProps,
    ...dispatchObject,
  };
};

export default connect(mapStateToProps, null, mergeProps)(Info);



