import React from 'react';
import classnames from 'classnames';
import ReactTooltip from 'react-tooltip';
import { icons } from '../../../constants/enums';
import Icon from '../../Icon/Icon';
import InfoPrechatForm from './InfoPrechatForm';
import style from './info.css';
import FeatureHelp from '../../FeatureHelp/FeatureHelp';
import * as localStorage from '../../../utils/localStorage';

class InfoPrechat extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showForm: props.ifpreChatEdit,
    };
    if (!props.ifpreChatEdit) {
      localStorage.deleteItem('prechatFormState');
    }
  }
  componentWillReceiveProps(nextProps) {
    if (this.props.visitorId !== nextProps.visitorId || this.props.chatId !== nextProps.chatId) {
      this.setState({
        showForm: false,
      });
    } else {
      this.setState({
        showForm: nextProps.ifpreChatEdit,
      });
    }
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (this.props.ifpreChatLoading !== nextProps.ifpreChatLoading ||
      this.state.showForm !== nextState.showForm ||
      this.props.latestName !== nextProps.latestName ||
      this.props.ip !== nextProps.ip ||
      this.props.latestEmail !== nextProps.latestEmail ||
      this.props.prechatPhone !== nextProps.prechatPhone ||
      this.props.prechatCompany !== nextProps.prechatCompany ||
      this.props.prechatProductService !== nextProps.prechatProductService ||
      this.props.departmentId !== nextProps.departmentId ||
      this.props.preChatReadOnly !== nextProps.preChatReadOnly ||
      this.props.newFeatureNotify !== nextProps.newFeatureNotify) {
      return true;
    }
    return false;
  }
  componentDidUpdate() {
    if (!this.state.showForm) {
      localStorage.deleteItem('prechatFormState');
    }
  }
  render() {
    const {
      latestName,
      ip,
      latestEmail,
      prechatPhone,
      prechatCompany,
      departmentSelected,
      departmentId,
      prechatProductService,
      onPreChatEditIconClick,
      preChatReadOnly,
      newFeatureNotify,
      onFeatrueClick,
      ...rest } = this.props;
    const initValues = {
      name: latestName || ip,
      email: latestEmail,
      phone: prechatPhone,
      company: prechatCompany,
      departmentId,
      productService: prechatProductService,
    };
    const ifShowFeature = !this.state.showForm && !preChatReadOnly && (newFeatureNotify & 1) == 0;
    return (
      <div>
        { ifShowFeature && (
        <div className={style.featrue}>
          <FeatureHelp
            ifShow={ifShowFeature}
            text="Click to edit Pre-chat info."
            featrueNumber={1}
            onGotitClick={
            () => {
              if (onFeatrueClick) onFeatrueClick(newFeatureNotify + 1);
            }}
          />
        </div>
        )}
        <div className={classnames(style.Container, style.editable, 'contentMenu', !this.state.showForm && style.notEdit)}>
          <div className={style.editIconWrapper} style={{ display: this.state.showForm ? 'none' : 'block' }}>
            <div tabIndex={-1} className={classnames(style.line, style.title)}>
              <h3>{latestName || ip}</h3>
            </div>
            <div tabIndex={-1} className={style.line}>
              <div className={style.wrapper} style={{ display: latestEmail ? 'block' : 'none' }}>
                <Icon className={style.icon} data-tip="Email" data-for="prechatTooltip" type={icons.sendEmail} color="#aaa" size={16} />
                <span className={style.text}>
                  {latestEmail}
                </span>
              </div>
              <div className={style.wrapper} style={{ display: prechatPhone ? 'block' : 'none' }}>
                <Icon className={style.icon} data-tip="Phone" data-for="prechatTooltip" type={icons.waitting} color="#aaa" size={16} />
                <span className={classnames(style.phone, style.text)}>
                  {prechatPhone}
                </span>
              </div>
              <div className={style.wrapper} style={{ display: prechatCompany ? 'block' : 'none' }}>
                <Icon className={style.icon} data-tip="Company" data-for="prechatTooltip" type={icons.company} color="#aaa" size={16} />
                <span className={style.text}>
                  {prechatCompany}
                </span>
              </div>
              <div className={style.wrapper} style={{ display: prechatProductService ? 'block' : 'none' }}>
                <Icon className={style.icon} data-tip="Product and Service" data-for="prechatTooltip" type={icons.productService} color="#aaa" size={16} />
                <span className={style.text}>
                  {prechatProductService}</span>
              </div>
              <div className={style.wrapper} style={{ display: departmentSelected ? 'block' : 'none' }}>
                <Icon className={style.icon} data-tip="Department" data-for="prechatTooltip" type={icons.department} color="#aaa" size={16} />
                <span className={style.text}>{rest.ifDepartmentEnabled && departmentSelected}</span>
              </div>
            </div>
            { !preChatReadOnly &&
            <Icon
              type={icons.typing} color="#aaa" size={14}
              className={style.editIcon} onClick={onPreChatEditIconClick}
            />
            }
          </div>
          { this.state.showForm &&
            <InfoPrechatForm
              initValues={initValues}
              {...rest}
            />
          }
          { !this.state.showForm &&
            <ReactTooltip id="prechatTooltip" effect="solid" place="bottom" class={style.tooltip} />
          }
        </div>
      </div>
    );
  }
}

InfoPrechat.propTypes = {
  preChatReadOnly: React.PropTypes.bool,
  ifDepartmentEnabled: React.PropTypes.bool,
  ifpreChatEdit: React.PropTypes.bool,
  latestName: React.PropTypes.string,
  ip: React.PropTypes.string,
  latestEmail: React.PropTypes.string,
  prechatPhone: React.PropTypes.string,
  prechatCompany: React.PropTypes.string,
  prechatProductService: React.PropTypes.string,
  departmentId: React.PropTypes.number,
  departmentSelected: React.PropTypes.string,
  handleSubmitpreChat: React.PropTypes.func,
  onPreChatEditIconClick: React.PropTypes.func,
  onPreChatCancelClick: React.PropTypes.func,
  ifpreChatLoading: React.PropTypes.bool,
  newFeatureNotify: React.PropTypes.number,
  onFeatrueClick: React.PropTypes.func,
  visitorId: React.PropTypes.number,
  chatId: React.PropTypes.number,
};
InfoPrechat.defaultProps = {
  ifDepartmentEnabled: false,
  ifpreChatEdit: false,
};

export default InfoPrechat;



